<template>
  <div
    id="main"
  >
    <el-card style="margin-top: 20px;width: 86%">
      <el-col
        :span="3"
        style='margin-left: 30px;'
      >
        <el-input
          placeholder=""
          v-model="formValue.goodsname"
          clearable
          prefix-icon="el-icon-search"
        >
        </el-input>
      </el-col>
      <el-col
        :span="3"
        style='margin-left: 5px;'
      >
        <el-select
          v-model="formValue.auditstatus"
          clearable
          filterable
          placeholder=""
        >
          <el-option
            v-for="item in status"
            :label="item.dicValue"
            :value="item.dicKey"
            :key="item.dicKey"
          >
          </el-option>
        </el-select>
      </el-col>
      <div
        class="block"
        style="display: inline-block;margin-left: 5px;"
      >
        <el-date-picker
          v-model="formValue.effectivetime"
          type="date"
          placeholder=""
          format="yyyy  MM  dd "
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </div>
      <div
        class="block"
        style="display: inline-block;margin-left: 5px"
      >
        <el-date-picker
          v-model="formValue.failuretime"
          type="date"
          placeholder=""
          format="yyyy  MM  dd "
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </div>
      <el-button
        type="primary"
        icon="el-icon-search"
        style='margin-left: 20px;'
        @click="query"
      >
        
      </el-button>
      <el-button
        type="success"
        icon="el-icon-circle-plus"
        style='margin-left: 5px;'
        @click="dialogFormVisible1 = true,resetForm('insertFormRe')"
      >
        
      </el-button>
      <el-button
        type="success"
        icon="el-icon-s-promotion"
        style='margin-left: 5px;'
        @click="draftVisible = true,getAllDrft()"
      >
        
      </el-button>
    </el-card>

    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 86%"
      @selection-change="handleSelectionChange"
      highlight-current-row
      @current-change="handleCurrentChange"
      :header-cell-style="headClass"
      :cell-style="rowClass"
    >
      <el-table-column
        type="selection"
        width="55"
      >
      </el-table-column>
      <el-table-column
        prop="goodsname"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="goodstock"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="oldsale"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="newsale"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="effectivetime"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="failuretime"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="auditstatus"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        label=""
        width="520"
      >
        <template slot-scope="scope">
          <el-button
            type="success"
            icon="el-icon-full-screen"
            style='margin-left: 5px;'
            @click="viewDialogForm = true"
          >
            
          </el-button>
          <el-button
            type="warning"
            icon="el-icon-edit"
            style='margin-left: 5px;'
            @click="dialogFormVisible2 = true"
            v-if="scope.row.auditstatus != ''"
          >
            
          </el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            style='margin-left: 5px;'
            @click="open()"
            v-if="scope.row.auditstatus != ''"
          >
            
          </el-button>
          <el-button
            type="primary"
            icon="el-icon-copy-document"
            style='margin-left: 5px;'
            @click="handleAuthorizationMenu(scope.$index, scope.row),trackVisible= true"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange1"
      :current-page.sync="page.pageNum"
      :page-sizes="[3, 4, 5, 6]"
      :page-size="page.pageSize"
      layout="sizes, prev, pager, next"
      :total="page.total"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="viewDialogForm"
    >
      <table
        border="1px"
        cellpadding="20px"
        cellspacing="0"
        id="myTable"
      >
        <tr>
          <td class="onLeft">id</td>
          <td class="onRight"><span v-text="updateForm.goodsid"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.goodsname"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.goodstock"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight">
            <span></span>
            <span v-text="updateForm.oldprice"></span>
            <span style="margin-left: 200px"></span>
            <span v-text="updateForm.oldsale"></span>
          </td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight">
            <span></span>
            <span v-text="updateForm.newprice"></span>
            <span style="margin-left: 200px"></span>
            <span v-text="updateForm.newsale"></span>
          </td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.effectivetime"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.failuretime"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.reasons"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.remark"></span></td>
        </tr>
      </table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="viewDialogForm = false"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="dialogFormVisible1"
    >
      <el-form
        :model="insertForm"
        ref="insertFormRe"
      >
        <table
          border="1px"
          cellpadding="20px"
          cellspacing="0"
          id="myTable"
        >
          <tr>
            <td class="onLeft">id</td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="insertForm.goodsid"
                  autocomplete="off"
