                ></el-input>
              </el-col>
              <el-button
                type="success"
                icon="el-icon-s-operation"
                style='margin-left: 220px;'
                @click="getGoodsFormOtherPage"
              >
                
              </el-button>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="insertForm.goodsname"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="insertForm.goodstock"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="insertForm.oldprice"
                  autocomplete="off"
                  placeholder=""
