                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft">:</td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="calc"
                  autocomplete="off"
                  @input="calcprice"
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="insertForm.newprice"
                  autocomplete="off"
                  placeholder=""
                ></el-input>
              </el-col>
              <el-col
                span="5"
                style="margin-left: 60px;"
              >
                <el-input
                  v-model="insertForm.newsale"
                  placeholder=""
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <div
                class="block"
                style="display: inline-block;margin-left: 5px;"
              >
                <el-date-picker
                  v-model="insertForm.effectivetime"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                >
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <div
                class="block"
                style="display: inline-block;margin-left: 5px;"
              >
                <el-date-picker
                  v-model="insertForm.failuretime"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                >
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="insertForm.reasons"
                autocomplete="off"
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="insertForm.remark"
                autocomplete="off"
              ></el-input>
            </td>
          </tr>
        </table>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible1 = false,remove()"> </el-button>
        <el-button
          type="success"
          @click="dialogFormVisible1 = false, draftInsert()"
        > </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible1 = false, formalInsert()"
        > </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="dialogFormVisible2"
    >
      <el-form
        :model="updateForm"
        ref="insertFormRe"
      >
        <table
          border="1px"
          cellpadding="20px"
          cellspacing="0"
          id="myTable"
        >
          <tr>
            <td class="onLeft">id</td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="updateForm.goodsid"
                  autocomplete="off"
                  disabled
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.goodsname"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.goodstock"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="updateForm.oldprice"
                  autocomplete="off"
                  placeholder=""
                  disabled
                ></el-input>
              </el-col>
              <el-col
                span="5"
                style="margin-left: 60px;"
              >
                <el-input
                  v-model="updateForm.oldsale"
                  placeholder=""
                  disabled
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="updateForm.newprice"
                  autocomplete="off"
                  placeholder=""
                ></el-input>
              </el-col>
              <el-col
                span="5"
                style="margin-left: 60px;"
              >
                <el-input
                  v-model="updateForm.newsale"
                  placeholder=""
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <div
                class="block"
                style="display: inline-block;margin-left: 5px;"
              >
                <el-date-picker
                  v-model="updateForm.effectivetime"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                >
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <div
                class="block"
                style="display: inline-block;margin-left: 5px;"
              >
                <el-date-picker
                  v-model="updateForm.failuretime"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                >
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.reasons"
                autocomplete="off"
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.remark"
                autocomplete="off"
              ></el-input>
            </td>
          </tr>
        </table>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible2 = false, reUpdate()"
        > </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="trackVisible"
    >
      <el-table
        :data="track"
        tooltip-effect="dark"
        style="width: 70%"
        @selection-change="handleSelectionChange"
        highlight-current-row
        @current-change="handleCurrentChange"
        :header-cell-style="headClass"
        :cell-style="rowClass"
      >
        <el-table-column
          prop="approner"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="aduittime"
          label=""
          width="250"
        >
        </el-table-column>
        <el-table-column
          prop="auditstatus"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="auditopinion"
          label=""
          width="150"
        >
        </el-table-column>
      </el-table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="trackVisible = false"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="draftVisible"
    >
      <el-table
        :data="draftTable"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        highlight-current-row
        @current-change="handleCurrentChange"
        :header-cell-style="headClass"
        :cell-style="rowClass"
      >
        <el-table-column
          prop="goodsname"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="goodstock"
          label=""
          width="55"
        >
        </el-table-column>
        <el-table-column
          prop="oldsale"
          label=""
          width="55"
        >
        </el-table-column>
        <el-table-column
          prop="newsale"
          label=""
          width="55"
        >
        </el-table-column>
        <el-table-column
          prop="effectivetime"
          label=""
          width="200"
        >
        </el-table-column>
        <el-table-column
          prop="failuretime"
          label=""
          width="200"
        >
        </el-table-column>
        <el-table-column
          label=""
          width="200"
        >
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-upload"
              style='margin-left: 5px;'
              @click="drafthandle(scope.$index, scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="draftVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      draftTable: [],
      calc: '',
      status: [],
      tableData: [],
      multipleSelection: [],
      page: {
        // pageSize: "",
        // pageNum: "",
        // total: "",
        // pages: ""
      },
      formValue: {},
      delNum: '',
      // 
      viewDialogForm: false,
      // 
      formLabelWidth: '120px',
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      trackVisible: false,
      draftVisible: false,
      updateForm: {},
      insertForm: {},
      track: []
    };
  },
  methods: {
    remove () {
      sessionStorage.removeItem("product");
    },
    reUpdate () {
      this.updateForm.auditstatus = 0;
      this.updateForm.auditlevel = 1;
      this.update();
    },
    getGoodsFormOtherPage () {
      this.$router.push("/main/acquireProudct")
    },
    queryStatus () {
      this.$axios.get("/audit/getStatus")
        .then(res => {
          this.status = res.data;
        });
    },
    insert () {
      this.$axios.post("/audit", this.insertForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.remove();
          }
          this.query();
        });
    },
    formalInsert () {
      this.insertForm.auditstatus = 0;
      this.insertForm.auditlevel = 1;
      this.insert();
    },
    draftInsert () {
      this.insertForm.auditstatus = 3;
      this.insert();
    },
    update () {
      this.$axios.put("/audit", this.updateForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        });
    },
    delSelected () {
      this.$axios.delete("/audit/" + this.delNum)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        })
    },
    open () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delSelected();
      })
    },
    query (param) {
      let data = param;
      // if (this.formValue.goodsname != "" || this.formValue.auditstatus != "") {
      if (this.formValue != undefined) {
        Object.assign(this.formValue, param);
        data = this.formValue;
      }
      this.$axios.get("/audit/allForm", {
        params: data
      })
        .then(res => {
          this.tableData = res.data.list; // 
          this.page = res.data; // 
        })
    },
    handleSizeChange (pageSize) { // 
      this.query({
        "pageNum": this.page.pageNum,
        "pageSize": pageSize
      })
    },
    handleCurrentChange1 (pageNum) { // 
      this.query({
        "pageNum": pageNum,
        "pageSize": this.page.pageSize
      });
    },
    handleSelectionChange (val) {
      if (val != undefined) {
        this.multipleSelection = val;
      }
    },
    handleCurrentChange (val) {
      if (val != undefined) {
        console.log(val);
        this.updateForm = val;
        this.delNum = val.id;
        this.trackId = val.goodsid;
      }
    },
    headClass () { //
      return "text-align:center"
    },
    rowClass () { //
      return "text-align:center"
    },
    resetForm () {
      if (this.insertForm != undefined) {
        this.insertForm = {};
        this.calc = '';
      }
    },
    calcprice () {
      this.insertForm.newsale = this.insertForm.oldsale * this.calc;
    },
    handleAuthorizationMenu (index, row) {
      this.getAllTrack(row.goodsid);
    },
    getAllTrack (param) {
      this.$axios.get("/audit/trace?id=" + param)
        .then(res => {
          this.track = res.data;
          console.log(this.track);
        });
    },
    getAllDrft () {
      this.$axios.get("/audit/draft")
        .then(res => {
          this.draftTable = res.data;
        })
    },
    drafthandle (index, row) {
      this.updateForm = {};
      this.updateForm.id = row.id;
      this.updateForm.auditstatus = 0;
      this.updateForm.auditlevel = 1;
      this.update();
    },
    getProduct () {
      let productStr = sessionStorage.getItem("product");
      let product = JSON.parse(productStr);
      if (product != undefined) {
        this.dialogFormVisible1 = true;
        this.insertForm.goodsid = product.gcode;
        this.insertForm.goodsname = product.gname;
        this.insertForm.goodstock = product.gminsum;
        this.insertForm.oldprice = product.gmarketprize;
        this.insertForm.oldsale = product.gmallprize;
      }

    }
  },
  created () {
    this.query();
    this.queryStatus();
    this.getProduct();
  },
}
</script>

<style scoped>
.onLeft {
  text-align: right;
}

.onRight {
  width: 500px;
}

#myTable {
  border: 1px lightblue solid;
}
</style>