<!--   todo -->
<template>
<div id='main'>
<el-card class="box-card">

  <!--  -->
  <el-form :inline="true" class="demo-form-inline">
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" v-model="searchForm.deptName" style="width:120px" >
          <el-option v-for='item in deptList' :key='item.deptName' :value='item.deptId' :label='item.deptName'></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" v-model="searchForm.accountStatus">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
    </el-form-item>
    <el-form-item label=":">
      <el-input v-model="searchForm.roleName" @keyup.enter.native="search"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input  v-model="searchForm.description" @keyup.enter.native="search"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="success" @click="search"></el-button>
      <el-button type="warning" @click="reset"></el-button>
        <el-button type="info" @click="handleInsert"></el-button>
    </el-form-item>
  </el-form>

  <!-- table -->
  <el-table :data="tableData" style="width: 100%" >
    <el-table-column prop="rid" label="" width="80px">
    </el-table-column>
    <el-table-column prop="roleCode" label="" >
    </el-table-column>
    <el-table-column prop="roleName" label="">
    </el-table-column>
    <el-table-column prop="description" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="roleStatus" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="auth" label="" show-overflow-tooltip>
    </el-table-column>

    <el-table-column label="" width="150px">
      <template slot-scope="scope">
        <!--  -->
        <el-button size="mini" @click="handleEdit(scope.row),operatedRowId=scope.$index"></el-button>
        <!-- bookIdbookIdDialog -->
        <el-button type="danger" size="mini"  @click="deletionId=scope.row.rid, delDialog=true"></el-button>
      </template>
    </el-table-column>
  </el-table>

  <!--  -->
  <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
    :current-page.sync="page.pageNum" :page-sizes="[2, 3, 5, 10]" :page-size="page.pageSize"
    layout="sizes, prev, pager, next" :total="page.total">
  </el-pagination>

    <!--  -->
  <el-dialog title="" :visible.sync="delDialog" width="30%">
    <span>, ?</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="delDialog= false"> </el-button>
      <el-button type="primary" @click="delDialog = false,deleteById()"> </el-button>
    </span>
  </el-dialog>

    <!---->
  <el-dialog title="" :visible.sync="addDialog" width="30%">
    <el-form>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.roleCode"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.description"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.roleStatus">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>


      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.auth">
          <el-option v-for='item in authList' :key='item.authName' :value='item.authId' :label='item.authName'></el-option>
        </el-select>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="clearForm(),getCheckedKeys()"> </el-button>
        <el-button type="primary" @click="handleEvent(), clearForm()"> </el-button>
    </div>
  </el-dialog>
 
</el-card>
</div>
</template>

<script>
export default {
  data() {
    return {
      formLabelWidth: '25%',
      tableData: [],
      deptList:[],
      page: {}, //pageSize,pageNum,total
      // row index
      operatedRowId:'',
      // id
      deletionId:-1,
      delDialog:false,//Dialog
      addDialog:false,
      action:'',
      searchForm:{},
      addForm:{
        roleCode : '',
        roleName : '',
        description : '',
        roleStatus: '',
        auth:'',
      },
      authList:[],
    }
  },
  methods: {
    getCheckedKeys() {
      console.log(this.$refs.tree.getCheckedKeys());
    },
    clearForm(){
      this.addDialog = false
      this.addForm = {}
    },

    handleEvent(){
      if(this.action=='insert'){
        this.$axios.post("/role/insertRole", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }else{
        this.$axios.put("/role/updateRole", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }
    },

    handleEdit(row){
      this.addDialog = true;
      this.addForm.rid = row.rid
      this.addForm.roleCode = row.roleCode
      this.addForm.roleName = row.roleName;
      this.addForm.description = row.description;
      this.addForm.roleStatus = row.roleStatus;
      this.addForm.auth = row.auth;
      this.action = 'update';
    },

    handleInsert(){
      this.addForm = {}
      this.addDialog=true
      this.action='insert'
    },

    deleteById(){
      this.$axios.delete("/role/deleteRole/"+this.deletionId).then(res=>{
        console.log(res)
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
      })
    },

    reset(){
      this.searchForm={}
      this.handlePageQuery(this.page.pageNum, this.page.pageSize)
      this.$refs.tree.setCheckedKeys([]);

    },
    search(){
      let param={pageNum:this.page.pageNum,pageSize:this.page.pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/role/searchRoles",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
    },
    query() {
      this.$axios.get("/role/getAllRoleWithPage").then(res => {
        this.tableData = res.data.list; //
        this.page = res.data;
      })
    },
    getAllDepts(){
      this.$axios.get("/dept/getAllDept").then(res => {
        this.deptList = res.data; //
      })
    },
    getAllAuth(){
      this.$axios.get("/auth/getAllAuth").then(res => {
        this.authList = res.data; //
      })
    },
    handleSizeChange(pageSize) {
      this.handlePageQuery(this.page.pageNum, pageSize)
    },
    handleCurrentChange(pageNum) {
      this.handlePageQuery(pageNum, this.page.pageSize)
    },
    //
    handlePageQuery(pageNum, pageSize){
      let param={pageNum:pageNum,pageSize:pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/role/searchRoles",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
    }
  },
  created() {
    this.query();
    this.getAllAuth();
    this.getAllDepts();
  }
}
</script>

<style scoped>
#main{
  /* border: 1px solid black; */
  margin-left: 3%;
  margin-right: 3%;
}
.page {
  float: right;
  margin-top: 20px;
}
.el-pagination {
    white-space: nowrap;
    padding: 15px 5px;
    color: #303133;
    font-weight: 700;
}
.selectBoxWidth {
  /* width: 100%; */
  text-align: left;
  width:110px;
}
.addBoxWidth{
  width: 100%;
}
</style>