<template>
  <div id="main">
    <el-card class="box-card">
      <!---->
      <el-form
        :inline="true"
        :model="queryForm"
        class="demo-form-inline"
      >
        <el-form-item label="">
          <el-input v-model="queryForm.presentName"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-cascader
            :options="typeOptions"
            :props="{ value: 'typeId', label: 'typeName'}"
            :show-all-levels="false"
            @change="handleChange"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="queryForm.presentBrand"
            placeholder=""
          >
            <el-option
              v-for="item in brandOptions"
              :key="item.dic_key"
              :label="item.dic_value"
              :value="item.dic_key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="queryForm.presentColor"
            placeholder=""
          >
            <el-option
              v-for="item in colorOptions"
              :key="item.dic_key"
              :label="item.dic_value"
              :value="item.dic_key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="queryForm.checkState"
            placeholder=""
          >
            <el-option
              v-for="item in checkStateOptions"
              :key="item.dic_key"
              :label="item.dic_value"
              :value="item.dic_key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="query()"
          ></el-button>
        </el-form-item>
        <el-button
          type="primary"
          @click="queryEmpty()"
        ></el-button>
      </el-form>
      <hr>
      <!---->
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          prop="checkId"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.checkId }}</span>
        </el-table-column>
        <el-table-column
          prop="presentName"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentName }}</span>
        </el-table-column>
        <el-table-column
          prop="presentType"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentType }}</span>
        </el-table-column>
        <el-table-column
          prop="presentBrand"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentBrand }}</span>
        </el-table-column>
        <el-table-column
          prop="presentColor"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentColor }}</span>
        </el-table-column>
        <el-table-column
          prop="presentPrice"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentPrice }}</span>
        </el-table-column>
        <el-table-column
          prop="presentNumber"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentNumber }}</span>
        </el-table-column>
        <el-table-column
          prop="checkState"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.checkState }}</span>
        </el-table-column>
        <el-table-column
          prop="channelHandle"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.channelHandle }}</span>
          <template slot-scope="scope">
            <el-button
              v-show="scope.row.checkState==''"
              size="mini"
              type="warning"
              round
              icon="el-icon-edit"
              @click="lookDialogFormVisible = true,handleLook(scope.row)"
            >
            </el-button>
            <el-button
              size="mini"
              type="success"
              round
              icon="el-icon-search"
              @click="checkDialogFormVisible = true,handleLook(scope.row),queryCheck(scope.row)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <div
        class="block"
        style="text-align: center"
      >
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.pageNum"
          :page-sizes="[5,10,15,20]"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total"
        >
        </el-pagination>
      </div>
      <!---->
      <el-dialog
        title=""
        :visible.sync="lookDialogFormVisible"
      >
        <el-form :model="updateForm">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentName"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentType"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentPrice"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentNumber"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentColor"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentBrand"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.inventoryQuantity"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="textarea"
              v-model="updateForm.recordOpinion"
              autocomplete="off"
            ></el-input>
          </el-form-item>
        </el-form>
        <div
          slot="footer"
          class="dialog-footer"
          style="text-align: center"
        >
          <div
            slot="footer"
            class="dialog-footer"
          >
            <el-button @click="lookDialogFormVisible = false,cancel()"> </el-button>
            <el-button
              type="primary"
              @click="lookDialogFormVisible = false,updateFalse()"
            ></el-button>
            <el-button
              type="primary"
              @click="lookDialogFormVisible = false,updateTrue()"
            ></el-button>
          </div>
        </div>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="checkDialogFormVisible"
      >
        <el-form :model="lookForm">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentName"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentType"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentPrice"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentNumber"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentColor"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentBrand"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.inventoryQuantity"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
        </el-form>
        <!---->
        <el-table
          :data="checkTableData"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column
            prop="recordAuditor"
            label=""
            width="180"
          >
            <span
              slot-scope="scope"
              style="margin-left: 10px"
            >{{ scope.row.recordAuditor }}</span>
          </el-table-column>
          <el-table-column
            prop="recordTime"
            label=""
            width="180"
          >
            <span
              slot-scope="scope"
              style="margin-left: 10px"
            >{{ scope.row.recordTime }}</span>
          </el-table-column>
          <el-table-column
            prop="checkState"
            label=""
            width="180"
          >
            <span
              slot-scope="scope"
              style="margin-left: 10px"
            >{{ scope.row.checkState }}</span>
          </el-table-column>
          <el-table-column
            prop="recordOpinion"
            label=""
            width="180"
          >
            <span
              slot-scope="scope"
              style="margin-left: 10px"
            >{{ scope.row.recordOpinion }}</span>
          </el-table-column>
        </el-table>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      updateForm: {
        checkId: '',
        recordId: '',
        recordAuditor: '',
        recordOpinion: '',
        checkState: '',
        recordTime: '',
      },
      typeOptions: [],
      checkStateOptions: [],
      brandOptions: [],
      colorOptions: [],
      firstOption: [],
      secondOption: [],
      lookForm: {},
      queryForm: {},
      checkDialogFormVisible: false,
      lookDialogFormVisible: false,
      formLabelWidth: '120px',
      checkTableData: [],
      tableData: [],
      page: {},
      presentPage: {},
    }
  },
  methods: {
    //
    updateTrue () {
      this.updateForm.checkState = "2";
      this.$axios.put('/GoodsCheckRecordHandler', this.updateForm)
        .then(response => {
          if (response.code === 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.query();
          } else {
            this.$message.error('');
          }
        }).catch(error => {
          console.log(error)
          this.$message.error('');
        });
    },
    updateFalse () {
      this.updateForm.checkState = "1";
      this.$axios.put('/GoodsCheckRecordHandler', this.updateForm)
        .then(response => {
          if (response.code === 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
            this.query();
          } else {
            this.$message.error('');
          }
        }).catch(error => {
          console.log(error)
          this.$message.error('');
        });
    },
    handleSizeChange (val) {
      this.query({
        pageNum: this.page.pageNum,
        pageSize: val
      })
    },
    // ----
    handleCurrentChange (val) {
      this.query({
        pageNum: val,
        pageSize: this.page.pageSize
      })
    },
    queryEmpty () {
      this.queryForm.presentName = '';
      this.queryForm.presentType = '';
      this.queryForm.presentColor = '';
      this.queryForm.presentBrand = '';
      this.queryForm.checkState = '';
    },
    query (param) {
      if (param === undefined) param = {}
      Object.assign(param, this.queryForm);
      this.$axios.get('/GoodsCheckHandler', {
        params: param
      }).then(response => {
        if (response.code === 20000) {
          this.tableData = response.data.list;
          this.page = response.data;
        } else {
          this.$message.error('');
        }
      }).catch(error => {
        console.log(error)
        this.$message.error('');
      })
    },
    //
    queryCheck (row) {
      this.$axios.get('/GoodsCheckRecordHandler/selectGoodsCheckRecordByCheckId/' + row.checkId
      ).then(response => {
        if (response.code === 20000) {
          this.checkTableData = response.data;
        } else {
          this.$message.error('');
        }
      }).catch(error => {
        console.log(error)
        this.$message.error('');
      })
    },
    //
    handleChange (value) {
      this.queryForm.presentType = value[value.length - 1];
    },
    //
    queryType () {
      this.$axios.get('/goodsType/selectTree',
      ).then(response => {
        if (response.code === 20000) {
          this.$nextTick(() => { this.typeOptions = this.getTreeData(response.data) }); //
          //domdom
        } else {
          this.$message.error('');
        }
      }).catch(error => {
        console.log(error)
        this.$message.error('');
      })
    },
    //
    queryCheckState () {
      this.$axios.get('/presentCheck/getCheckState',
      ).then(response => {
        if (response.code === 20000) {
          this.checkStateOptions = response.data;
        } else {
          this.$message.error('');
        }
      }).catch(error => {
        console.log(error)
        this.$message.error('');
      })
    },
    //
    queryBrand () {
      this.$axios.get('/present/getBrand',
      ).then(response => {
        if (response.code === 20000) {
          this.brandOptions = response.data;
        } else {
          this.$message.error('');
        }
      }).catch(error => {
        console.log(error)
        this.$message.error('');
      })
    },
    //
    queryColor () {
      this.$axios.get('/present/getColor',
      ).then(response => {
        if (response.code === 20000) {
          this.colorOptions = response.data;
        } else {
          this.$message.error('');
        }
      }).catch(error => {
        console.log(error)
        this.$message.error('');
      })
    },

    //
    cancel () {
      this.$message({
        type: 'info',
        message: ''
      });
    },
    //
    handleLook (row) {
      this.lookForm.presentName = row.presentName;
      this.lookForm.presentNumber = row.presentNumber;
      this.lookForm.goodsModelNumber = row.goodsModelNumber;
      this.lookForm.presentType = row.presentType;
      this.lookForm.presentBrand = row.presentBrand;
      this.lookForm.presentColor = row.presentColor;
      this.lookForm.goodsDepartment = row.goodsDepartment;
      this.lookForm.presentPrice = row.presentPrice;
      this.lookForm.goodsCost = row.goodsCost;
      this.lookForm.inventoryQuantity = row.inventoryQuantity;
      this.lookForm.physicalPrice = row.physicalPrice;
      this.lookForm.checkState = row.checkState;
      this.updateForm.recordOpinion = "";
      this.updateForm.checkId = row.checkId;
      this.updateForm.checkState = "";
    },
    tableRowClassName ({rowIndex }) {
      if (rowIndex % 2 === 0) {
        return 'success-row';
      }
      return '';
    },
    getTreeData (data) {
      // json
      for (let i = 0; i < data.length; i++) {
        if (data[i].children == null || data[i].children.length < 1) {
          // subMenusubMenuundefined
          data[i].children = undefined;
        } else {
          // subMenu  
          this.getTreeData(data[i].children);
        }
      }
      return data;
    },
  },
  components: {},
  computed: {},
  created () {
    this.query();
    this.queryType();
    this.queryBrand();
    this.queryCheckState();
    this.queryColor();
  },
  mounted () { }
}
</script>

<style scoped>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

.el-input {
  width: 90%;
}

.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.box-card {
  width: 100%;
}
</style>