<template>
  <div id="main">
    <el-card class="box-card">
      <!---->
      <el-form
        :inline="true"
        :model="queryForm"
        class="demo-form-inline"
      >
        <el-button
          type="success"
          @click="addDialogFormVisible = true,addPresent()"
        ></el-button>
        <el-form-item label="">
          <el-input v-model="queryForm.presentName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="queryForm.presentModelNumber"></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-cascader
            :options="typeOptions"
            :props="{ value: 'typeId', label: 'typeName'}"
            :show-all-levels="false"
            @change="handleChangeQueryType"
          >
          </el-cascader>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="queryForm.presentBrand"
            placeholder=""
          >
            <el-option
              v-for="item in brandOptions"
              :key="item.dic_key"
              :label="item.dic_value"
              :value="item.dic_key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-select
            v-model="queryForm.presentColour"
            placeholder=""
          >
            <el-option
              v-for="item in colorOptions"
              :key="item.dic_key"
              :label="item.dic_value"
              :value="item.dic_key"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            @click="query()"
          ></el-button>
        </el-form-item>
        <el-button
          type="primary"
          @click="queryEmpty"
        ></el-button>
      </el-form>
      <hr>
      <!---->
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          prop="presentId"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentId }}</span>
        </el-table-column>
        <el-table-column
          prop="presentName"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentName }}</span>
        </el-table-column>
        <el-table-column
          prop="presentModelNumber"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentModelNumber }}</span>
        </el-table-column>
        <el-table-column
          prop="presentType"
          label=""
          width="180"
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentType }}</span>
        </el-table-column>
        <el-table-column
          prop="presentMinimumStock"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentMinimumStock }}</span>
        </el-table-column>
        <el-table-column
          prop="presentPrice"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentPrice }}</span>
        </el-table-column>
        <el-table-column
          prop="presentCost"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentCost }}</span>
        </el-table-column>
        <el-table-column
          prop="presentColour"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentColour }}</span>
        </el-table-column>
        <el-table-column
          prop="presentBrand"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentBrand }}</span>
        </el-table-column>
        <el-table-column
          prop="presentState"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.presentState }}</span>
        </el-table-column>
        <el-table-column
          prop="channelHandle"
          label=""
        >
          <span
            slot-scope="scope"
            style="margin-left: 10px"
          >{{ scope.row.channelHandle }}</span>

          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              round
              icon="el-icon-search"
              @click="lookDialogFormVisible = true,handleLook(scope.row)"
            >
            </el-button>
            <el-button
            v-show="scope.row.presentState==''"
              size="mini"
              type="warning"
              round
              icon="el-icon-edit"
              @click="updateDialogFormVisible = true,handleUpdate(scope.row)"
            >
            </el-button>
            <el-button
            v-show="scope.row.presentState==''"
              size="mini"
              type="danger"
              round
              icon="el-icon-delete"
              @click="handleDelete(scope.row)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-dialog
        title=""
        :visible.sync="lookDialogFormVisible"
      >
        <el-form :model="lookForm">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentName"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentType"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentModelNumber"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentPrice"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentCost"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentDepartment"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentMinimumStock"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentColour"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="lookForm.presentBrand"
              autocomplete="off"
              disabled
            ></el-input>
          </el-form-item>
        </el-form>
      </el-dialog>
      <!---->
      <el-dialog
        title=""
        :visible.sync="updateDialogFormVisible"
      >
        <el-form :model="addForm">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="updateForm.presentName"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-cascader
              :options="typeOptions"
              :props="{ value: 'typeId', label: 'typeName'}"
              @change="handleChangeUpdateType"
              :show-all-levels="false"
            >
            </el-cascader>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="updateForm.presentModelNumber"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="updateForm.presentPrice"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="updateForm.presentCost"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-select
              v-model="updateForm.presentDepartment"
              placeholder=""
              style="width: 100%"
            >
              <el-option
                v-for="item in deptOptions"
                :key="item.dic_key"
                :label="item.dic_value"
                :value="item.dic_key"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="updateForm.presentMinimumStock"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-select
              v-model="updateForm.presentColour"
              placeholder=""
            >
              <el-option
                v-for="item in colorOptions"
                :key="item.dic_key"
                :label="item.dic_value"
                :value="item.dic_key"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-select
              v-model="updateForm.presentBrand"
              placeholder=""
            >
              <el-option
                v-for="item in brandOptions"
                :key="item.dic_key"
                :label="item.dic_value"
                :value="item.dic_key"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div
          slot="footer"
          class="dialog-footer"
          style="text-align: center"
        >
          <div
            slot="footer"
            class="dialog-footer"
          >
            <el-button @click="updateDialogFormVisible = false,cancel()"> </el-button>
            <el-button
              type="primary"
              @click="updateDialogFormVisible = false,update()"
            > </el-button>
          </div>
        </div>
      </el-dialog>
      <!---->
      <div
        class="block"
        style="text-align: center"
      >
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="page.pageNum"
          :page-sizes="[5,10,15,20]"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total"
        >
        </el-pagination>
      </div>
      <!---->
      <el-dialog
        title=""
        :visible.sync="addDialogFormVisible"
      >
        <el-form :model="addForm">
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="addForm.presentName"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-cascader
              :options="typeOptions"
              :props="{ value: 'typeId', label: 'typeName'}"
              @change="handleChangeAddType"
              :show-all-levels="false"
            >
            </el-cascader>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="addForm.presentModelNumber"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="addForm.presentPrice"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="addForm.presentCost"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-select
              v-model="addForm.presentDepartment"
              placeholder=""
            >
              <el-option
                v-for="item in deptOptions"
                :key="item.dic_key"
                :label="item.dic_value"
                :value="item.dic_key"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-input
              v-model="addForm.presentMinimumStock"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-select
              v-model="addForm.presentColour"
              placeholder=""
            >
              <el-option
                v-for="item in colorOptions"
                :key="item.dic_key"
                :label="item.dic_value"
                :value="item.dic_key"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            :label-width="formLabelWidth"
          >
            <el-select
              v-model="addForm.presentBrand"
              placeholder=""
            >
              <el-option
                v-for="item in brandOptions"
                :key="item.dic_key"
                :label="item.dic_value"
                :value="item.dic_key"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div
          slot="footer"
          class="dialog-footer"
          style="text-align: center"
        >
          <el-button
            type="primary"
            @click="addSubmit(),addDialogFormVisible = false "
          > </el-button>
          <el-button @click="addDialogFormVisible = false,cancel()"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      typeOptions: [],
      deptOptions: [],
      brandOptions: [],
      colorOptions: [],
      firstClass: '',
      firstSelect: '',
      firstOption: [],
      secondOption: [],
      lookForm: {},
      updateForm: {
        presentId: '',
        presentName: '',
        presentType: '',
        presentColour: '',
        presentBrand: '',
        presentModelNumber: '',
        presentMinimumStock: '',
        presentPrice: '',
        presentCost: '',
        presentDepartment: '',
        presentState: '',
      },
      queryForm: {
        presentId: '',
        presentName: '',
        presentType: '',
        presentColour: '',
        presentBrand: '',
      },
      addDialogFormVisible: false,
      lookDialogFormVisible: false,
      updateDialogFormVisible: false,
      addForm: {
        presentId: '',
        presentName: '',
        presentType: '',
        presentColour: '',
        presentBrand: '',
        presentModelNumber: '',
        presentMinimumStock: '',
        presentPrice: '',
        presentCost: '',
        presentDepartment: '',
        presentState: '',
      },
      formLabelWidth: '120px',
      value: [],
      formInline: { user: '', region: '', },
      tableData: [],
      page: {},
  };
},
methods: {
  handleSizeChange(val) {
    this.query({
      pageNum: this.page.pageNum,
      pageSize: val
    })
  },
  // ----
  handleCurrentChange(val) {
    this.query({
      pageNum: val,
      pageSize: this.page.pageSize
    })
  },
  queryEmpty() {
    this.queryForm.presentColour = '';
    this.queryForm.presentBrand = '';
    this.queryForm.presentColour = '';
    this.queryForm.presentModelNumber = '';
    this.queryForm.presentName = '';
    this.queryForm.presentType = '';
  },
  query(param) {
    if (param === undefined) param = {}
    Object.assign(param, this.queryForm);
    this.$axios.get('/present', {
      params: param
    }).then(response => {
      if (response.code === 20000) {
        this.tableData = response.data.list;
        this.page = response.data;
      } else {
        this.$message.error('');
      }
    }).catch(error => {
      console.log(error)
      this.$message.error('');
    })
  },
  //
  handleChangeAddType(value){
    this.addForm.presentType = value[value.length - 1];
  },
  //
  handleChangeQueryType(value){
    this.queryForm.presentType = value[value.length - 1];
  },
  //
  handleChangeUpdateType(value){
    this.updateForm.presentType = value[value.length - 1];
  },
  //
  queryPresentDepartment() {
    this.$axios.get('/present/getDepartment',
    ).then(response => {
      if (response.code === 20000) {
        this.deptOptions = response.data;
      } else {
        this.$message.error('');
      }
    }).catch(error => {
      console.log(error)
      this.$message.error('');
    })
  },
  //
  queryPresentBrand() {
    this.$axios.get('/present/getBrand',
    ).then(response => {
      if (response.code === 20000) {
        this.brandOptions = response.data;
      } else {
        this.$message.error('');
      }
    }).catch(error => {
      console.log(error)
      this.$message.error('');
    })
  },
  //
  queryPresentColor() {
    this.$axios.get('/present/getColor',
    ).then(response => {
      if (response.code === 20000) {
        this.colorOptions = response.data;
      } else {
        this.$message.error('');
      }
    }).catch(error => {
      console.log(error)
      this.$message.error('');
    })
  },
  //
  queryPresentType() {
    this.$axios.get('/goodsType/selectTree',
    ).then(response => {
      if (response.code === 20000) {
        this.$nextTick(() => { this.typeOptions = this.getTreeData(response.data) }); //
        //domdom
      } else {
        this.$message.error('');
      }
    }).catch(error => {
      console.log(error)
      this.$message.error('');
    })
  },
  addSubmit() {
    this.$axios.post('/present/addPresent', this.addForm).then(response => {
      if (response.code === 20000) {
        this.$message({
          message: '',
          type: 'success'
        });
        this.query();
      } else {
        this.$message.error('');
      }
    })
  },
  //
  update() {
    this.$axios.put('../present', this.updateForm)
      .then(response => {
        if (response.code === 20000) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.query();
        } else {
          this.$message.error('');
        }
      }).catch(error => {
              console.log(error)

        this.$message.error('');
      });
  },
  //
  cancel() {
    this.$message({
      type: 'info',
      message: ''
    });
  },
  addPresent() {
    this.addForm.presentId = '';
    this.addForm.presentName = '';
    this.addForm.presentModelNumber = '';
    this.addForm.presentType = '';
    this.addForm.presentMinimumStock = '';
    this.addForm.presentPrice = '';
    this.addForm.presentCost = '';
    this.addForm.presentDepartment = '';
    this.addForm.presentColour = '';
    this.addForm.presentBrand = '';
    this.addForm.presentState = '';
  },
  //
  handleLook(row){
    this.$axios.get('/present/selectPresentById/' + row.presentId
    ).then(response => {
      if (response.code === 20000) {
        this.lookForm = response.data;
      } else {
        this.$message.error('');
      }
    }).catch(error => {
            console.log(error)

      this.$message.error('');
    })

  },
  //
  handleUpdate(row){
    this.updateForm.presentId = row.presentId;
    this.updateForm.presentName = row.presentName;
    this.updateForm.presentModelNumber = row.presentModelNumber;
    this.updateForm.presentType = "";
    this.updateForm.presentMinimumStock = row.presentMinimumStock;
    this.updateForm.presentPrice = row.presentPrice;
    this.updateForm.presentCost = row.presentCost;
    this.updateForm.presentDepartment = "";
    this.updateForm.presentColour = "";
    this.updateForm.presentBrand = "";
    this.updateForm.presentState = "";
  },
  //
  handleDelete(row) {
    this.$confirm(',?', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    }).then(() => {
      this.$axios.put("/present/updateState/" + row.presentId)
        .then(response => {
          this.query();
          if (response.code === 20000) {
            this.$message({
              type: 'success',
              message: '!'
            });
          } else {
            this.$message({
              type: 'info',
              message: ''
            });
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    }).catch(() => {
      this.$message({
        type: 'info',
        message: ''
      });
    });
  },
  tableRowClassName({rowIndex }) {
    if (rowIndex % 2 === 0) {
      return 'success-row';
    }
    return '';
  },
  getTreeData(data) {
    // json
    for (let i = 0; i < data.length; i++) {
      if (data[i].children == null || data[i].children.length < 1) {
        // subMenusubMenuundefined
        data[i].children = undefined;
      } else {
        // subMenu  
        this.getTreeData(data[i].children);
      }
    }
    return data;
  },
},
computed: { },
created() {
  this.query();
  this.queryPresentType();
  this.queryPresentColor();
  this.queryPresentDepartment();
  this.queryPresentBrand();
},
mounted() { }
}
</script>

<style scoped>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}

.el-input {
  width: 90%;
}

.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.box-card {
  width: 100%;
}
</style>