<!--  -->
<template>
<div id="app">
    <template>
        <el-card class="box-card">

            <el-form :inline="true" :model="logForm" ref="logForm" >
                <el-form-item label="" prop="userName">
                    <el-input v-model="logForm.userName" placeholder=""></el-input>
                </el-form-item>

                <el-form-item label="" prop="userModel">
                    <el-select v-model="logForm.userModel" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" prop="userMethod">
                    <el-select v-model="logForm.userMethod" placeholder="">
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>

                <span></span>
                <el-date-picker
                        v-model="targetTime"
                        type="daterange"
                        start-placeholder=""
                        end-placeholder=""
                        format="yyyy  MM  dd "
                        value-format="yyyy-MM-dd 00:00:00">
                </el-date-picker>
                <el-form-item>
                    <el-button type="primary" @click="queryByType"></el-button>
                    <el-button type="success" @click="resetForm('logForm')"></el-button>
                </el-form-item>
            </el-form>

            <el-table
                    :data="tableData"
                    style="width: 100%"
                    :row-class-name="tableRowClassName">
                <el-table-column
                        prop="id"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createTime"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="userName"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="userModel"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="userMethod"
                        label=""
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="userIp"
                        label="ip"
                        width="180">
                </el-table-column>
            </el-table>

            <div class="block">
                <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[2, 3, 4, 5]" :page-size="pageSize"
                               layout="total, sizes, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>

        </el-card>
    </template>
</div>
</template>

<script>
export default {
 data() {
            return {
                currentPage:{},
                pageSize:{},
                total:{},
                tableData: [],
                logForm: {
                    userName: '',
                    userModel: '',
                    userMethod: ''
                },
                targetTime: [],
            }
        },
 methods: {
            tableRowClassName({rowIndex}) {
                if (rowIndex % 2 == 1) {
                    return 'success-row';
                }
                return '';
            },
            resetForm(formName) {
                this.targetTime = [];
                this.$refs[formName].resetFields();
            },
            query(param) {
                this.$axios.get('/log/selectAll',{
                    params: param
                }).then(response => {
                    console.log(response)
                        if (response.code === 20000) {
                            this.total=response.data.total;
                            this.pageSize=response.data.pageSize;
                            this.currentPage=response.data.pageNum;
                            this.tableData = response.data.list;
                        } else {
                            this.$message({
                                type: 'info',
                                message: ','
                            });
                        }
                    }).catch((error) => {
                      console.log(error)
                    this.$message({
                        type: 'warning',
                        message: ','
                    });
                });
            },
            queryByType() {
                var beginTime = this.targetTime[0]
                var endTime = this.targetTime[1]
                var param = {
                    "userName": this.logForm.userName,
                    "userModel": this.logForm.userModel,
                    "userMethod": this.logForm.userMethod,
                    "beginTime": beginTime,
                    "endTime": endTime,
                    'page': "1",
                    'pageSize': this.pageSize
                };
                console.log(param)
                this.query(param);
            },

            handleSizeChange(val) {
                this.query({
                    'page': "1",
                    'pageSize': val
                })
            },
            handleCurrentChange(val) {
                this.query({
                    'page': val,
                    'pageSize': this.pageSize
                })

            },
        },
  created() {
    this.query();
  }
}
</script>

<style scoped>
 .el-table .success-row {
        background: #f0f9eb;
    }

    .box-card {
        width: 100%;
    }
</style>