<!--   todo -->
<template>
<div id='main'>
<el-card class="box-card">

  <!--  -->
  <el-form :inline="true" class="demo-form-inline">
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" v-model="searchForm.deptName" style="width:120px" >
          <el-option v-for='item in deptList' :key='item.deptName' :value='item.deptId' :label='item.deptName'></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" v-model="searchForm.accountStatus">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" style="width:80px;" v-model="searchForm.position">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" style="width:80px;" v-model="searchForm.role">
          <el-option v-for='item in roleList' :key='item.rid' :value='item.roleName' :label='item.roleName'></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" style="width:80px;" v-model="searchForm.gender">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
    </el-form-item>

    <el-form-item label=":">
      <el-input v-model="searchForm.uname" @keyup.enter.native="search"></el-input>
    </el-form-item>
    <el-form-item label=":">
      <el-input  v-model="searchForm.username" @keyup.enter.native="search"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="success" @click="search"></el-button>
      <el-button type="warning" @click="reset"></el-button>
        <el-button type="info" @click="handleInsert"></el-button>
    </el-form-item>
  </el-form>

  <!-- table -->
  <el-table :data="tableData" style="width: 100%" >
    <el-table-column prop="uid" label="" width="55px">
    </el-table-column>
    <el-table-column prop="username" label="" >
    </el-table-column>
    <el-table-column prop="uname" label="">
    </el-table-column>
    <el-table-column prop="deptName" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="gender" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="accountStatus" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="position" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="uphone" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="email" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="role" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column label="" width="150px">
      <template slot-scope="scope">
        <!--  -->
        <el-button size="mini" @click="handleEdit(scope.row),operatedRowId=scope.$index"></el-button>
        <!-- bookIdbookIdDialog -->
        <el-button type="danger" size="mini"  @click="deletionId=scope.row.uid, delDialog=true"></el-button>
      </template>
    </el-table-column>
  </el-table>

  <!--  -->
  <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
    :current-page.sync="page.pageNum" :page-sizes="[2, 3, 5, 10]" :page-size="page.pageSize"
    layout="sizes, prev, pager, next" :total="page.total">
  </el-pagination>

    <!--  -->
  <el-dialog title="" :visible.sync="delDialog" width="30%">
    <span>, ?</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="delDialog= false"> </el-button>
      <el-button type="primary" @click="delDialog = false,deleteById()"> </el-button>
    </span>
  </el-dialog>

    <!---->
  <el-dialog title="" :visible.sync="addDialog" width="30%">
    <el-form>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.password"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.uname"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.uphone"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.deptName">
          <el-option v-for='item in deptList' :key='item.deptName' :value='item.deptId' :label='item.deptName'></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.accountStatus">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.role">
          <el-option v-for='item in roleList' :key='item.rid' :value='item.roleName' :label='item.roleName'></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.position">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder=""  v-model="addForm.gender">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>      
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="clearForm()"> </el-button>
        <el-button type="primary" @click="handleEvent(), clearForm()"> </el-button>
    </div>
  </el-dialog>
 
</el-card>
</div>
</template>

<script>
export default {
  data() {
    return {
      formLabelWidth: '120px',
      tableData: [],
      deptList:[],
      page: {}, //pageSize,pageNum,total
      // row index
      operatedRowId:'',
      // id
      deletionId:-1,
      delDialog:false,//Dialog
      addDialog:false,
      action:'',
      searchForm:{},
      addForm:{
        username : '',
        password : '',
        uname : '',
        uphone : '',
        email : '',
        deptName : '',
        role : '',
        position : '',
        gender : '',
        accountStatus : '',
      },
      updateForm:{},
      roleList:{}
    }
  },
  methods: {
    clearForm(){
      this.addDialog = false
      this.addForm = {}
    },

    handleEvent(){
      if(this.action=='insert'){
        this.$axios.post("/user/insertUser", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }else{
        this.$axios.put("/user/updateUser", this.addForm).then(res => {
          console.log(res)
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }
    },

    handleEdit(row){
      this.addForm.uid = row.uid
      this.addForm.username = row.username;
      this.addForm.password = row.password;
      this.addForm.uname = row.uname;
      this.addForm.uphone = row.uphone;
      this.addForm.email = row.email;
      this.addForm.deptName = row.deptName;
      this.addForm.role = row.role;
      this.addForm.position = row.position;
      this.addForm.gender = row.gender;
      this.addForm.accountStatus = row.accountStatus;
      this.action = 'update';
      this.addDialog = true;
    },

    handleInsert(){
      this.addForm = {}
      this.addDialog=true
      this.action='insert'
    },

    deleteById(){
      this.$axios.delete("/user/deleteUser/"+this.deletionId).then(res=>{
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
      })
    },

    reset(){
      this.searchForm={}
      this.handlePageQuery(this.page.pageNum, this.page.pageSize)
    },

    search(){
      let param={pageNum:this.page.pageNum,pageSize:this.page.pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/user/searchUsers",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
      // this.$axios.post("/user/searchUsers", this.searchForm,{params:{pageNum:1,pageSize:3}}).then(res => {
      //   this.tableData = res.data.list;
      //   // this.page = res.data;
      // })
    },
    query() {
      this.$axios.get("/user/getAllUsers").then(res => {
        this.tableData = res.data.list; //
        this.page = res.data;
      })
    },
    getAllDepts(){
      this.$axios.get("/dept/getAllDept").then(res => {
        this.deptList = res.data; //
      })
    },
    getAllRoles(){
      this.$axios.get("/role/getAllRole").then(res => {
        this.roleList = res.data; //
      })
    },
    handleSizeChange(pageSize) {
      this.handlePageQuery(this.page.pageNum, pageSize)
    },
    handleCurrentChange(pageNum) {
      this.handlePageQuery(pageNum, this.page.pageSize)
    },
    //
    handlePageQuery(pageNum, pageSize){
      let param={pageNum:pageNum,pageSize:pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/user/searchUsers",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
    }
  },
  created() {
    this.query();
    this.getAllDepts();
    this.getAllRoles();
  }
}
</script>

<style scoped>
#main{
  /* border: 1px solid black; */
  margin-left: 3%;
  margin-right: 3%;
}
.page {
  float: right;
  margin-top: 20px;
}
.el-pagination {
    white-space: nowrap;
    padding: 15px 5px;
    color: #303133;
    font-weight: 700;
}
.selectBoxWidth {
  /* width: 100%; */
  text-align: left;
  width:110px;
}
.addBoxWidth{
  width: 100%;
}
</style>