<template>
  <div
    id="main"
    v-clock
  >
    <div style="float:left;margin-left: 100px;margin-top: 20px;width: 150px;">
      <el-tree
        :data="treeData"
        :props="defaultProps"
        node-key="typeId"
        :default-expanded-keys="[1, 2, 4, 5, 7, 15]"
      >
      </el-tree>
    </div>
    <div style="float:left;padding-left: 130px;margin-top: 20px">
      <el-card>
        <el-col
          :span="3"
          style='margin-left: 30px;'
        >
          <el-input
            placeholder=""
            v-model="formValue.typeName"
            clearable
            prefix-icon="el-icon-search"
          >
          </el-input>
        </el-col>
        <el-col
          :span="3"
          style='margin-left: 5px;'
        >
          <el-input
            placeholder=""
            v-model="formValue.typeremark"
            clearable
            prefix-icon="el-icon-search"
          >
          </el-input>
        </el-col>
        <el-button
          type="primary"
          icon="el-icon-search"
          style='margin-left: 20px;'
          @click="query"
        >
          
        </el-button>
        <el-button
          type="danger"
          icon="el-icon-delete"
          style='margin-left: 5px;'
          @click="open()"
        >
          
        </el-button>
        <el-button
          type="warning"
          icon="el-icon-edit"
          style='margin-left: 5px;'
          @click="dialogFormVisible2 = true"
        >
          
        </el-button>
        <el-button
          type="success"
          icon="el-icon-circle-plus"
          style='margin-left: 5px;'
          @click="dialogFormVisible1 = true,resetForm('insertFormRe')"
        >
          
        </el-button>
      </el-card>
      <el-table
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        highlight-current-row
        @current-change="handleCurrentChange"
        :header-cell-style="headClass"
        :cell-style="rowClass"
      >
        <el-table-column
          type="selection"
          width="200"
        >
        </el-table-column>
        <el-table-column
          type="index"
          width="200"
          label=''
        >
        </el-table-column>
        <el-table-column
          prop="typeName"
          label=""
          width="200"
        >
        </el-table-column>
        <el-table-column
          prop="typeremark"
          label=""
          width="200"
        >
        </el-table-column>
        <el-table-column
          label=""
          width="200"
        >
          <template slot-scope="scope">
            <el-button
              type="success"
              icon="el-icon-eleme"
              style='margin-left: 5px;'
              @click="brandVisible = true,getBrand(scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange1"
        :current-page.sync="page.pageNum"
        :page-sizes="[3, 4, 5, 6]"
        :page-size="page.pageSize"
        layout="sizes, prev, pager, next"
        :total="page.total"
      >
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible1"
    >
      <el-form
        :model="insertForm"
        ref="insertFormRe"
      >
        <el-col span="12"></el-col>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="typeName"
        >
          <el-col span="12">
            <el-input
              v-model="insertForm.typeName"
              autocomplete="off"
            ></el-input>
          </el-col>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="typeremark"
        >
          <el-col span="12">
            <el-input
              v-model="insertForm.typeremark"
              autocomplete="off"
            ></el-input>
          </el-col>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="typePid"
        >
          <el-col span="12">
            <el-select
              v-model="insertForm.typePid"
              clearable
              filterable
              placeholder=""
            >
              <el-option
                v-for="item in parentMenus"
                :label="item.typeName"
                :value="item.typeId"
                :key="item.typeId"
              >
              </el-option>
            </el-select>
          </el-col>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible1 = false, insert()"
        > </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible2"
    >
      <el-form
        :model="updateForm"
        ref="updateFormRe"
      >
        <el-col span="12"></el-col>
        <el-form-item
          label="id"
          :label-width="formLabelWidth"
          prop="id"
        >
          <el-col span="12">
            <el-input
              v-model="updateForm.typeId"
              autocomplete="off"
              :disabled="true"
            ></el-input>
          </el-col>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-col span="12">
            <el-input
              v-model="updateForm.typeName"
              autocomplete="off"
            ></el-input>
          </el-col>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
        >
          <el-col span="12">
            <el-input
              v-model="updateForm.typeremark"
              autocomplete="off"
            ></el-input>
          </el-col>
        </el-form-item>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible2 = false, update()"
        > </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="brandVisible"
    >
      <el-transfer
        v-model="value"
        :data="data"
        :titles="['', '']"
      >
      </el-transfer>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="brandVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="brandVisible = false,saveBrand()"
        > </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    // const generateData = _ => {
    //   const data = [];
    //   const brands = ['LG', '', '', '', '1x', '', ''];
    //   brands.forEach((brand, index) => {
    //     data.push({
    //       label: brand,
    //       key: index + 1
    //     });
    //   });
    //   return data;
    // };
    return {
      // data: generateData(),
      value: [],
      tableData: [],
      multipleSelection: [],
      page: {
        // pageSize: "",
        // pageNum: "",
        // total: "",
        // pages: ""
      },
      formValue: {},
      delNum: [],
      treeData: [],
      defaultProps: {
        children: 'children',
        label: 'typeName',
        id: 'typeId'
      },
      // 
      brandVisible: false,
      formLabelWidth: '120px',
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      updateForm: {},
      insertForm: {},
      parentMenus: []
    };
  },
  methods: {
    saveBrand () {
      this.updateForm.typeBrand = this.value.join();
      this.update();
    },
    getBrand (row) {
      this.updateForm = {};
      if (row.typeBrand != undefined) {
        let splits = row.typeBrand.split(",")
        this.value = splits.map(Number);
      }
    },
    queryParent () {
      this.$axios.get("/goodsType/selectParent")
        .then(res => {
          this.parentMenus = res.data;
        });
    },
    insert () {
      this.$axios.post("/goodsType", this.insertForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
          this.queryTree();
        });
    },
    update () {
      this.$axios.put("/goodsType", this.updateForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
          this.queryTree();
        });
    },
    queryTree () {
      this.$axios.get("/goodsType/selectTree")
        .then(res => {
          this.treeData = res.data;
        });
    },
    delSelected () {
      this.delNum = [];
      for (let key in this.multipleSelection) {
        console.log(key + '---' + this.multipleSelection[key].typeId);
        this.delNum.push(this.multipleSelection[key].typeId);
      }
      this.$axios.delete("/goodsType/" + this.delNum.join())
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
          this.queryTree();
        })
    },
    open () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delSelected();
      })
    },
    query (param) {
      let data = param;
      if (this.formValue != undefined) {
        Object.assign(this.formValue, param);
        data = this.formValue;
      }
      this.$axios.get("/goodsType/selectAll", {
        params: data
      }).then(res => {
        this.tableData = res.data.list; // 
        this.page = res.data;
      })
    },
    handleSizeChange (pageSize) { // 
      this.query({
        "pageNum": this.page.pageNum,
        "pageSize": pageSize
      })
    },
    handleCurrentChange1 (pageNum) { // 
      this.query({
        "pageNum": pageNum,
        "pageSize": this.page.pageSize
      });
    },
    handleSelectionChange (val) {
      if (val != undefined) {
        this.multipleSelection = val;
      }
    },
    handleCurrentChange (val) {
      if (val != undefined) {
        this.updateForm = val;
      }
    },
    headClass () { //
      return "text-align:center"
    },
    rowClass () { //
      return "text-align:center"
    },
    resetForm (formName) {
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    }
  },
  components: {},
  computed: {},
  created () {
    this.query();
    this.queryTree();
    this.queryParent();
  },
  mounted () { }
}
</script>

<style scoped>
[v-clock] {
  display: none;
}
</style>