<template>
  <div id="main">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item><a href="#"></a></el-breadcrumb-item>
    </el-breadcrumb>
    <hr>
    <el-button
      type="info"
      plain
      @click="daishenhe"
    ></el-button>
    <el-button
      type="warning"
      plain
      @click="shenheguo "
    ></el-button>
    <el-button
      type="danger"
      plain
      @click="shenhebu"
    ></el-button>
    <hr>
    <!--  -->
    <el-form
      ref="Factoryform"
      :model="Factoryform"
      label-width="100px"
      :inline="true"
    >

      <el-form-item
        label=""
        style="margin-left:5%;"
      >
        <div class="block">
          <el-date-picker
            style="width: 170px;"
            v-model="Factoryform.beginTimeA"
            type="date"
            placeholder=""
            format="yyyy  MM  dd "
            value-format="yyyy-MM-dd"
            clearable
          >
          </el-date-picker>
        </div>
      </el-form-item>
      <el-form-item label="">
        <div class="block">
          <el-date-picker
            style="width: 170px;"
            v-model="Factoryform.endTimeA"
            type="date"
            placeholder=""
            format="yyyy  MM  dd "
            value-format="yyyy-MM-dd"
            clearable
          >
          </el-date-picker>
        </div>
      </el-form-item>

      <el-form-item label=":">
        <el-input
          v-model="Factoryform.DeliveryOrderNo"
          style="width: 170px"
          clearable
        ></el-input>
      </el-form-item>

      <br>
      <el-form-item
        label=""
        style="margin-left:5%;"
      >
        <div class="block">
          <el-date-picker
            style="width: 170px;"
            v-model="Factoryform.beginTimeP"
            type="date"
            placeholder=""
            format="yyyy  MM  dd "
            value-format="yyyy-MM-dd"
            clearable
          >
          </el-date-picker>
        </div>
      </el-form-item>
      <el-form-item label="">
        <div class="block">
          <el-date-picker
            style="width: 170px;"
            v-model="Factoryform.endTimeP"
            type="date"
            placeholder=""
            format="yyyy  MM  dd "
            value-format="yyyy-MM-dd"
            clearable
          >
          </el-date-picker>
        </div>
      </el-form-item>

      <el-form-item label=":">
        <el-input
          v-model="Factoryform.Creator"
          style="width: 170px;"
          clearable
        ></el-input>
      </el-form-item>

      <el-form-item label=":">
        <el-select
          v-model="Factoryform.OutboundSign"
          placeholder=""
          style="width: 150px;"
          clearable
        >

          <el-option
            label=""
            value=""
          ></el-option>
          <el-option
            label=""
            value=""
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label=":">
        <el-input
          v-model="Factoryform.address"
          style="width: 150px;"
          clearable
        ></el-input>
      </el-form-item>

      <el-form-item>
        <el-button
          type="primary"
          @click="rushenhesearch"
        ></el-button>
        <el-button
          type="primary"
          @click="Exportdocument"
        ></el-button>
      </el-form-item>

    </el-form>
    <hr />
    <!--   -->
    <el-table
      :data="shenhetableData"
      highlight-current-row
      style="width: 99% "
      ref="tableData"
    >
      <el-table-column
        prop="rid"
        label=""
        width="100"
      >
      </el-table-column>

      <el-table-column
        prop="deliveryorderno"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column
        prop="outboundsign"
        label=""
        width="180"
      >
      </el-table-column>
      <el-table-column
        prop="creator"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column
        prop="preparationtime"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column
        prop="approvedby"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column
        prop="approvaltime"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            @click="shenhe(scope.row)"
            type="warning"
          ></el-button>
          <el-button
            @click="RFAudit(scope.row) "
            type="warning"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handlePageChange"
      :current-page="page.pageNum"
      :page-sizes="[5,8]"
      :page-size="page.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.total"
      style="margin-left: 36%;margin-top: 350px;"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="shenheformmotai"
      width="80%"
      high="500px"
    >
      <el-form
        ref="yijianform"
        :model="yijianform"
        label-width="100px"
        :rules="rules"
      >
        <el-table
          :data="neirong"
          border
          style="width: 100%;margin-top: 30px;"
        >
          <el-table-column
            prop="goodsid"
            label=""
            width="120"
          >
          </el-table-column>
          <el-table-column
            prop="goodsbrand"
            label=""
            width="150"
          >
          </el-table-column>
          <el-table-column
            prop="goodsname"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="goodsmodel"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="goodscolor"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="goodssupplier"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="goodstrack"
            label=""
          >
          </el-table-column>
          <el-table-column
            prop="goodsleixing"
            label=""
          >
          </el-table-column>
        </el-table>
        <el-table
          :data="neirongaddress"
          border
          style="width: 300px;margin-top: 50px;margin-left: 70%;"
        >
          <el-table-column
            prop="address"
            label=""
          >
          </el-table-column>
        </el-table>

        <el-form-item
          label=":"
          prop="shenpijilu"
          style="margin-left: 68%;margin-top: 20px; "
        >
          <el-input
            type="textarea"
            v-model="yijianform.shenpijilu"
            style="width: 200px; "
          ></el-input>
        </el-form-item>

        <el-form-item
          label=":"
          prop="state"
          style="margin-left: 68%;margin-top: 20px; "
        >
          <el-select
            v-model="yijianform.state"
            placeholder=""
          >
            <el-option
              label=""
              value=""
            ></el-option>
            <el-option
              label=""
              value=""
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-button
        @click="yijiansub('yijianform')"
        style="margin-left: 70%;"
      ></el-button>
      <el-button @click="shenheformmotai=false"></el-button>

      <el-pagination
        @size-change="shenheSizeChange"
        @current-change="shenhePageChange"
        :current-page="shenhepage.pageNum"
        :page-sizes="[5, 8]"
        :page-size="shenhepage.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="shenhepage.total"
        style="margin-left: 36%;margin-top: 60px;"
      >
      </el-pagination>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="RFAuditformmotai"
      width="80%"
      high="500px"
    >
      <el-table
        :data="RFAudittable"
        border
        style="width: 100%;margin-top: 30px;"
      >
        <el-table-column
          prop="goodsid"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="goodsbrand"
          label=""
          width="150"
        >
        </el-table-column>
        <el-table-column
          prop="goodsname"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="goodsmodel"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="goodscolor"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="goodssupplier"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="goodstrack"
          label=""
        >
        </el-table-column>
        
       
      
      </el-table>

      <el-table
        :data="RFAudittable2"
        border
        style="width: 70%;margin-top: 30px;margin-left: 30%;"
      >
        <el-table-column
          prop="address"
          label=""
          width="120"
        >
        </el-table-column>
       
        <el-table-column
          prop="approvedby"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="approvaltime"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="state"
          label=""
        >
        </el-table-column>
        <el-table-column
          prop="shenpijilu"
          label=""
        >
        </el-table-column>
      </el-table>
      <el-button
        @click="RFAuditformmotai=false"
        style="margin-left: 90%; margin-top: 50px;"
      ></el-button>

    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {

      // 
      Factoryform: {
        DeliveryOrderNo: "",
        OutboundSign: "",
        Creator: "",
        address: "",
        beginTimeP: '', //
        endTimeP: '', //
        beginTimeA: '', //
        endTimeA: '', //
        ppageNum: 5,
        ppageSize: 8,
        state: "",
      },
      shenheformmotai: false,
      RFAuditformmotai: false,
      shenhetableData: [{}], //  
      shenheneirong: [],


      yijianform: {
        shenpijilu: "",
        state: "",
        deliveryorderno: "",
      },
      RFAudittable: [{}],
      RFAudittable2: [{}],

      rules: {
        shenpijilu: [{ required: true, message: '', trigger: 'change' }],
        state: [{ required: true, message: '', trigger: 'blur' }]
      },

      page: {},
      neirong: [],

      neirongaddress: [{
        address: "",
      }],
      shenhepage: {
        pageNum: '5',
        pageSize: '8',

      },
      RFAuditpage: {
        pageNum: '5',
        pageSize: '8',
      },
      number: "",
    };
  },
  methods: {
    Exportdocument () {
      location.href = "http://localhost:8080/CFactoryshenhe/Export/"
    },

    // 
    rushenhesearch () {

      this.$axios.post("/CFactoryshenhe/Cshenhesearch", this.Factoryform).then(response => {
        this.shenhetableData = response.data.list;
        this.page = response.data;
      }).catch(function (error) {
        console.log(error);
      })
    },
    handleSizeChange (pageSize) {
      //
      console.log(pageSize)
      this.Factoryform.ppageNum = this.page.pageNum
      this.Factoryform.ppageSize = pageSize
      this.rushenhesearch({})
    },
    handlePageChange (pageNum) {
      //
      this.Factoryform.ppageNum = pageNum,
        this.Factoryform.ppageSize = this.page.pageSize
      this.rushenhesearch({})
    },

    daishenhe () {
      this.Factoryform.state = ""
      this.rushenhesearch();
    },
    shenheguo () {
      this.Factoryform.state = ""
      this.rushenhesearch();
    },
    shenhebu () {
      this.Factoryform.state = ""
      this.rushenhesearch();
    },

    shenhe (row) {
      if (row.deliveryorderno != undefined) {
        this.number = row.deliveryorderno
      }
      this.$axios.get("/CFactoryshenhe/CFshenhe?" + "pageNumber=" + this.shenhepage.pageNum + "&pageSize=" + this.shenhepage.pageSize + "&RFshenheid=" + this.number).then(response => {
        this.neirong = response.data.list

        this.neirongaddress[0].address = this.neirong[0].goodsaddress
        this.shenhepage = response.data;

        this.shenheformmotai = true;
      }).catch(function (error) {
        console.log(error);
      })

    },
    shenheSizeChange (pageSize) {
      this.shenhepage.pageSize = pageSize,
      this.shenhe({})
    },
    shenhePageChange (pageNum) {     //
      this.shenhepage.pageNum = pageNum,
      this.shenhe({})
    },

    yijiansub (yijianform) {				//,
      this.yijianform.deliveryorderno = this.number

      this.$refs[yijianform].validate((valid) => {
        if (valid) {
          this.$axios.post("/CFactoryshenhe/yijiansub", this.yijianform).then(response => {
            if (response.data == 1) {
              this.$message({
                message: '',
                type: 'success'
              });

            } else {
              this.$message({
                message: '',
                type: 'warning'
              });
            }
          }).catch(function (error) {
            console.log(error);
          })


        }
      });
    },

    RFAudit (row) {
      if (row.deliveryorderno != undefined) {
        this.number = row.deliveryorderno
      }
      this.$axios.get("/CFactoryshenhe/CFAudit?" + "RFshenheid=" + this.number).then(response => {

        if (response.code == 20000) {

          this.RFAuditformmotai = true;
          this.RFAudittable = response.data.goods

          this.RFAudittable2[0] = response.data.fact
          console.log(response.data.data.goods);
        }

      }).catch(function (error) {
        console.log(error);
      })
    },
  },
  components: {},
  computed: {},
  created () {
    this.rushenhesearch(); //
  },
  mounted () { }
}
</script>

<style scoped>
</style>