<template>
  <div
    id="app"
    v-clock
  >
    <el-card style="margin-top: 20px;width: 86%">
      <el-col
        :span="3"
        style='margin-left: 30px;'
      >
        <el-input
          placeholder=""
          v-model="formValue.sname"
          clearable
          prefix-icon="el-icon-search"
        >
        </el-input>
      </el-col>
      <el-col
        :span="3"
        style='margin-left: 5px;'
      >
        <el-input
          placeholder=""
          v-model="formValue.contacts"
          clearable
          prefix-icon="el-icon-search"
        >
        </el-input>
      </el-col>
      <el-button
        type="primary"
        icon="el-icon-search"
        style='margin-left: 20px;'
        @click="query"
      >
        
      </el-button>
      <el-button
        type="success"
        icon="el-icon-circle-plus"
        style='margin-left: 5px;'
        @click="dialogFormVisible1 = true"
      >
        
      </el-button>
    </el-card>
    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 86%"
      @selection-change="handleSelectionChange"
      highlight-current-row
      @current-change="handleCurrentChange"
      :header-cell-style="headClass"
      :cell-style="rowClass"
    >
      <el-table-column
        type="selection"
        width="55"
      >
      </el-table-column>
      <el-table-column
        prop="sname"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="contacts"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="telephone"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="email"
        label=""
        width="160"
      >
      </el-table-column>
      <el-table-column
        prop="address"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="bank"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="account"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        label=""
        width="400"
      >
        <template slot-scope="scope">
          <el-button
            type="success"
            icon="el-icon-full-screen"
            style='margin-left: 5px;'
            @click="viewDialogForm = true"
          >
            
          </el-button>
          <el-button
            type="warning"
            icon="el-icon-edit"
            style='margin-left: 5px;'
            @click="dialogFormVisible2 = true"
            v-if="scope.row.auditstatus != ''"
          >
            
          </el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            style='margin-left: 5px;'
            @click="open()"
            v-if="scope.row.auditstatus != ''"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange1"
      :current-page.sync="page.pageNum"
      :page-sizes="[3, 4, 5, 6]"
      :page-size="page.pageSize"
      layout="sizes, prev, pager, next"
      :total="page.total"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="dialogFormVisible1"
    >
      <el-form
        :model="insertForm"
        ref="insertFormRe"
      >
      </el-form>

      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible1 = false"> </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible1 = false"
        > </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="dialogFormVisible2"
    >
      <el-form
        :model="updateForm"
        ref="insertFormRe"
      >
      </el-form>

      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible2 = false"
        > </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      tableData: [],
      multipleSelection: [],
      page: {
        // pageSize: "",
        // pageNum: "",
        // total: "",
        // pages: ""
      },
      formValue: {},
      delNum: '',
      // 
      formLabelWidth: '120px',
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      updateForm: {},
      insertForm: {},
    };
  },
  methods: {
    insert () {
      this.$axios.post("/audit", this.insertForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        });
    },
    update () {
      this.$axios.put("/audit", this.updateForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        });
    },
    delSelected () {
      this.$axios.delete("/audit/" + this.delNum)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        })
    },
    open () {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delSelected();
      })
    },
    query (param) {
      let data = param;
      if (this.formValue != undefined) {
        Object.assign(this.formValue, param);
        data = this.formValue;
        // console.log(data);
      }
      this.$axios.get("/supplier/selectAll", {
        params: data
      })
        .then(res => {
          this.tableData = res.data.list; // 
          this.page = res.data; // 
        })
    },
    handleSizeChange (pageSize) { // 
      this.query({
        "pageNum": this.page.pageNum,
        "pageSize": pageSize
      })
    },
    handleCurrentChange1 (pageNum) { // 
      this.query({
        "pageNum": pageNum,
        "pageSize": this.page.pageSize
      });
    },
    handleSelectionChange (val) {
      if (val != undefined) {
        this.multipleSelection = val;
      }
    },
    handleCurrentChange (val) {
      if (val != undefined) {
        console.log(val);
        this.updateForm = val;
        this.delNum = val.id;
        this.trackId = val.goodsid;
      }
    },
    headClass () { //
      return "text-align:center"
    },
    rowClass () { //
      return "text-align:center"
    }
  },
  components: {},
  computed: {},
  created () {
    this.query();

  },
  mounted () { }
}
</script>

<style scoped>
[v-clock] {
  display: none;
}
.onLeft {
  text-align: right;
}

.onRight {
  width: 500px;
}

#myTable {
  border: 1px lightblue solid;
}
</style>