import Vue from 'vue'
import App from './App'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import router from './router'
import axios from './utils/http'

import qs from 'qs'
import store from './store'
Vue.config.productionTip = false;

//  Vue.use()  new Vue() 
Vue.use(ElementUI)
Vue.prototype.$qs = qs;
Vue.prototype.$axios = axios;

/**
 * main.js vue App.vue 
 * index.htmlid'app' 
 * index.js 
 * App.vue <router-view> 
 */

new Vue({
       el: '#app', //
       //renderel
       render: h => h(App), //App 
       router,
       store,
       template: '<App/>'
})
