<template>
  <div id='main'>
    <!-- :model="form" , form -->
    <!-- :rules="rules" rules -->
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      label-width="80px"
      class="login-box"
    >
      <h3
        class="login-title"
        style="margin:0 auto 0px auto"
      ></h3>

      <div style="margin:10px 0px 10px 25px">
        <el-button
          id="passwordLogin"
          @click="password=true"
          type="text"
          :disabled="password"
        ></el-button>
        <el-button
          id="phoneLogin"
          @click="password=false"
          type="text"
          :disabled="!password"
        ></el-button>
      </div>

      <div v-show="password">
        <el-form-item
          label=""
          prop="username"
        >
          <el-input
            type="text"
            placeholder=""
            v-model="form.username"
          />
        </el-form-item>
        <el-form-item
          label=""
          prop="password"
        >
          <el-input
            type="password"
            placeholder=""
            v-model="form.password"
            @keyup.enter.native="onSubmit"
          />
        </el-form-item>
      </div>

      <!--  -->
      <div v-show="!password">
        <el-form-item label="">
          <el-input
            type="text"
            placeholder=""
            v-model="formII.uphone"
          />
        </el-form-item>

        <el-form-item label="">

          <el-input
            type="text"
            placeholder=""
            v-model="formII.valCode"
            style="width:62%"
            @keyup.enter.native="onSubmit"
          />
          <el-button
            v-show="sendAuthCode"
            @click="getAuthCode"
          ></el-button>
          <el-button
            v-show="!sendAuthCode"
            style="width:34%"
            disabled
          >{{authTime}} s</el-button>
        </el-form-item>

      </div>

      <el-form-item id="buttons">
        <el-button
          type="primary"
          v-on:click="onSubmit"
        ></el-button>
        <el-button type="success"></el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
export default {
  name: "Login",
  data () {
    return {
      sendAuthCode: true,
      authTime: 0,
      password: true,
      phone: false,
      form: {
        username: '',
        password: ''
      },
      formII: {
        uphone: "",
        valCode: "",
      },
      // el-formrules
      rules: {
        // el-form-itemprop
        username: [
          { required: true, message: '', trigger: 'blur' }
          // require:;message:trigger
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      dialogVisible: false
    }
  },
  methods: {
    getAuthCode () {
      //
      if(this.formII.uphone == ""){
        this.$message.error('');
      }else{
        this.$axios.post('/valCode', this.$qs.stringify({uphone:this.formII.uphone})).then(res => {
          console.log(res)
          if (res.code == 50000) {
            this.$message.error('');
          }
        })
        this.sendAuthCode = false;
        this.authTime = 60;
        var auth_timer = setInterval(() => {
          this.authTime--;
          if (this.authTime <= 0) {
            this.sendAuthCode = true;
            clearInterval(auth_timer);
          }
        }, 1000);
      }
    },
    onSubmit () {
      //form
      if (this.password) {
        this.$axios.post('/loginByPassword', this.form)
          .then(res => {
            if (res.code == 20001) {
              let loginUser = res.data.username;
              sessionStorage.setItem("user", JSON.stringify(loginUser));
              this.$store.dispatch('setUser', loginUser)
              this.$router.push("/main");//
            } else {
              this.$message.error('');
            }
          }).catch(function (error) {
            console.log(error);
          });
      } else {
        this.$axios.post('/loginByPhone', this.formII)
          .then(res => {
            if (res.code == 20001) {
              let loginUser = res.data.username;
              sessionStorage.setItem("user", JSON.stringify(loginUser));
              this.$store.dispatch('setUser', loginUser)
              this.$router.push("/main");//
            } else {
              this.$message.error('');
            }
          }).catch(function (error) {
            console.log(error);
          });
      }
    },

    beforeRouteEnter (to, from, next) {
      // 
      next(vm => {
        vm.$store.dispatch("setUser", null);
      });
    }
  }
}
</script>

<style scoped>
#main {
  width: 100%;
  height: 100%;
  margin: 0;
  background: url('../assets/b3.jpg') no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  border: 1px solid white;
}

.login-box {
  /* border: 1px solid #DCDFE6; */
  width: 380px;
  margin: 250px auto;
  padding: 35px 35px 15px 35px;
  border-radius: 5px;
  box-shadow: 0 0 25px #909399;
}

.login-title {
  text-align: center;
  margin: 0 auto 40px auto;
  color: #303133;
}
#buttons {
  margin-left: 10%;
}
/* >>> .el-input__inner{
    BACKGROUND-COLOR: transparent;
  } */
</style>