<template>
<div id='main'>
<el-card class="box-card">

  <!--  -->
  <el-form :inline="true" class="demo-form-inline">
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" v-model="searchForm.authName" style="width:120px" >
          <el-option v-for='item in distinctAuthList' :key='item' :value='item' :label='item'></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
        <el-select class="selectBoxWidth" placeholder="" v-model="searchForm.authStatus">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
    </el-form-item>
    <el-form-item label="">
      <el-input v-model="searchForm.roleName" @keyup.enter.native="search"></el-input>
    </el-form-item>
    <el-form-item label="">
      <el-input  v-model="searchForm.description" @keyup.enter.native="search"></el-input>
    </el-form-item>

    <el-form-item>
      <el-button type="success" @click="search"></el-button>
      <el-button type="warning" @click="reset"></el-button>
        <el-button type="info" @click="handleInsert"></el-button>
    </el-form-item>
  </el-form>

  <!-- table -->
  <el-table :data="tableData" style="width: 100%" >
    <el-table-column prop="authId" label="" width="120px">
    </el-table-column>
    <el-table-column prop="authName" label="" >
    </el-table-column>
    <el-table-column prop="roleName" label="">
    </el-table-column>
    <el-table-column prop="authStatus" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="description" label="" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="menuId" label="" show-overflow-tooltip>
      <template slot-scope="scope"> 
        <el-button round plain size="mini" @click="showMenuIds(scope.row)" ></el-button>
      </template>
    </el-table-column>

    <el-table-column label="" width="150px">
      <template slot-scope="scope"> 
        <!--  -->
        <el-button size="mini" @click="handleEdit(scope.row), setKeysForEdit(scope.row), operatedRowId=scope.$index"></el-button>
        <!-- bookIdbookIdDialog -->
        <el-button type="danger"  size="mini"  @click="deletionId=scope.row.authId, delDialog=true"></el-button>
      </template>
    </el-table-column>
  </el-table>

  <!--  -->
  <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
    :current-page.sync="page.pageNum" :page-sizes="[2, 3, 5, 10]" :page-size="page.pageSize"
    layout="sizes, prev, pager, next" :total="page.total">
  </el-pagination>

    <!--  -->
  <el-dialog title="" :visible.sync="delDialog" width="30%">
    <span>, ?</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="delDialog= false"> </el-button>
      <el-button type="primary" @click="delDialog = false,deleteById()"> </el-button>
    </span>
  </el-dialog>

    <!---->
  <el-dialog title="" :visible.sync="addDialog" width="30%">
    <el-form>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.authName"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.roleName"></el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
      <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" 
        placeholder="" v-model="addForm.description">
      </el-input>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-select class="addBoxWidth" placeholder="" v-model="addForm.authStatus">
        <el-option label="" value="1"></el-option>
        <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-tree :data="menuId" show-checkbox 
          node-key="id" ref="editTree" highlight-current :props="defaultProps">
        </el-tree>
      </el-form-item>

    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="clearForm()"> </el-button>
        <el-button type="primary" @click="handleEvent(), clearForm()"> </el-button>
    </div>
  </el-dialog>

  <!--  -->
  <el-dialog title="" :visible.sync="authDialog" width="30%">
    <el-form>
      <el-form-item label="" :label-width="formLabelWidth">
        <el-tree :data="menuId" show-checkbox default-expand-all
          node-key="id" ref="tree" highlight-current :props="defaultProps">
        </el-tree>
      </el-form-item>
    </el-form>
  </el-dialog>
 
</el-card>
</div>
</template>

<script>
export default {
  data() {
    return {
      formLabelWidth: '25%',
      tableData: [],
      page: {}, //pageSize,pageNum,total
      // row index
      operatedRowId:'',
      checkAuthRowId:'',
      // id
      deletionId:-1,
      delDialog:false,//Dialog
      addDialog:false,
      authDialog:false,
      action:'',
      searchForm:{},
      addForm:{
        authName : '',
        roleName : '',
        description : '',
        authStatus: '',
        menuId:'',
      },
      distinctAuthList:[],
      roleList:{},
      menuId: [
        {
          id: 100,
          label: '',
          children: [{
            id: 10001,
            label: '',
            children: [{
              id: 1000101,
              label: ''
            }, {
              id: 1000102,
              label: ''
            }, {             
               id: 1000103,
              label: ''
            }, {
              id: 1000104,
              label: ''
            }, {
              id: 1000105,
              label: ''
            }, {
              id: 1000106,
              label: ''
            }, {
              id: 1000107,
              label: ''
            }]
          },{
            id: 10002,
            label: '',
            children: [{
              id: 1000201,
              label: ''
            }, {
              id: 1000202,
              label: ''
            }, {
              id: 1000203,
              label: ''
            }, {
              id: 1000204,
              label: ''
            }, {
              id: 1000205,
              label: ''
            }]
          },]
        }, {
          id: 200,
          label: 'B2C',
          children: [{
            id: 20001,
            label: 'B2C'
          }, {
            id: 20002,
            label: 'B2C'
          }]
        }, {
          id: 300,
          label: '',
          children: [{
            id: 30001,
            label: 'B2C',
            children: [{
              id: 3000101,
              label: ''
            }, {
              id: 3000102,
              label: ''
            }]
          },{
            id: 30002,
            label: 'B2C',
            children: [{
              id: 3000201,
              label: ''
            }, {
              id: 3000202,
              label: ''
            }, {
              id: 3000203,
              label: ''
            }, {
              id: 3000204,
              label: ''
            }]
          },]
        },{
          id: 400,
          label: '',
          children: [{
            id: 40001,
            label: ''
          }, {
            id: 40002,
            label: ''
          }, {
            id: 40003,
            label: ''
          }, {
            id: 40004,
            label: ''
          }]
        },{
          id: 500,
          label: '',
          children: [{
            id: 50001,
            label: ''
          }, {
            id: 50002,
            label: ''
          }, {
            id: 50003,
            label: ''
          }]
        }
      ],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },
  methods: {
    showMenuIds(row){
      this.authDialog=true
      this.$nextTick(() =>{
        this.$refs.tree.setCheckedKeys([]);
        if(row.menuId!=null){
          var menuArr = row.menuId.split(',').map(Number);
          this.$refs.tree.setCheckedKeys(menuArr);
        }
      })
    },
    setKeysForEdit(row) {
      this.$nextTick(() =>{
        this.$refs.editTree.setCheckedKeys([]);
        if(row.menuId!=null){
          var menuArr = row.menuId.split(',').map(Number);
          console.log(menuArr)
          this.$refs.editTree.setCheckedKeys(menuArr);
        }
      })
    },
    clearForm(){
      this.addDialog = false
      this.addForm = {}
    },

    handleEvent(){
      this.addForm.menuId = this.$refs.editTree.getCheckedKeys().join(',');//menuId
      if(this.action=='insert'){
        this.$axios.post("/auth/insertAuth", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }else{
        this.$axios.put("/auth/updateAuth", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }
    },

    handleEdit(row){
      this.addDialog = true;
      this.addForm.authId = row.authId
      this.addForm.authName = row.authName
      this.addForm.roleName = row.roleName;
      this.addForm.description = row.description;
      this.addForm.authStatus = row.authStatus;
      this.action = 'update';
    },

    handleInsert(){
      this.addForm = {}
      this.addDialog=true
      this.$nextTick(() =>{
        this.$refs.editTree.setCheckedKeys([]);
      })
      this.action='insert'
    },

    deleteById(){
      this.$axios.delete("/auth/deleteAuth/"+this.deletionId).then(res=>{
        if(res.code == 20000){
          this.handlePageQuery(this.page.pageNum, this.page.pageSize)
          this.$message({
            message: '',
            type: 'success'
          });
        }else{
          this.$message({
            message: '',
            type: 'error'
          });
        }
      })
    },

    reset(){
      this.searchForm={}
      this.handlePageQuery(this.page.pageNum, this.page.pageSize)
      this.$refs.editTree.setCheckedKeys([]);

    },
    search(){
      let param={pageNum:this.page.pageNum,pageSize:this.page.pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/auth/searchAuth",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
      // this.$axios.post("/user/searchUsers", this.searchForm,{params:{pageNum:1,pageSize:3}}).then(res => {
      //   this.tableData = res.data.list;
      //   // this.page = res.data;
      // })
    },
    query() {
      this.$axios.get("/auth/getAllAuthWithPage").then(res => {
        this.tableData = res.data.list; //
        this.page = res.data;
      })
    },
    handleSizeChange(pageSize) {
      this.handlePageQuery(this.page.pageNum, pageSize)
    },
    handleCurrentChange(pageNum) {
      this.handlePageQuery(pageNum, this.page.pageSize)
    },
    //
    handlePageQuery(pageNum, pageSize){
      let param={pageNum:pageNum,pageSize:pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/auth/searchAuth",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
    },
    getDistinctAuth(){
      this.$axios.get("/auth/getDistinctAuth").then(res => {
        this.distinctAuthList = res.data; //
      })
    },
  },
  created() {
    this.query();
    this.getDistinctAuth();
  }
}
</script>

<style scoped>
#main{
  /* border: 1px solid black; */
  margin-left: 3%;
  margin-right: 3%;
}
.page {
  float: right;
  margin-top: 20px;
}
.el-pagination {
    white-space: nowrap;
    padding: 15px 5px;
    color: #303133;
    font-weight: 700;
}
.selectBoxWidth {
  /* width: 100%; */
  text-align: left;
  width:110px;
}
.addBoxWidth{
  width: 100%;
}
</style>