<template>
  <div id="app">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-form
      :inline="true"
      :model="formInline"
      class="demo-form-inline"
    >
      <el-form-item label="">
        <el-input
          v-model="formInline.bname"
          placeholder=""
        ></el-input>
      </el-form-item>
      <!--<option value=""></option>-->
      <el-form-item>
        <el-button
          type="primary"
          @click="onSubmit1"
        ></el-button>
        <el-button
          type="primary"
          @click="dialogTableVisible = true"
        ></el-button>
        <el-button
          type="primary"
          @click="onSubmit1"
        ></el-button>
      </el-form-item>
    </el-form>
    <template>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
      >
        <el-table-column
          prop="bid"
          label=""
          width="300"
        >
        </el-table-column>
        <el-table-column
          prop="bname"
          label=""
          width="300"
        >
        </el-table-column>
        <el-table-column
          prop="bsite"
          label=""
          width="500"
        >
        </el-table-column>
        <el-table-column
          prop="bdescribe"
          label=""
          width="300"
        >
        </el-table-column>
        <el-table-column
          label=""
          width="300"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.$index, scope.row),dialogTableVisible2 = true"
            >
            </el-button>
            <el-button
              size="mini"
              type="danger"
              @click="dialogTableVisible3 = true"
            >
            </el-button>
            <el-dialog
              title=""
              :visible.sync="dialogTableVisible3"
            >
              <p></p>
              <div
                slot="footer"
                class="dialog-footer"
              >
                <el-button @click="dialogTableVisible3=false"> </el-button>
                <el-button
                  type="primary"
                  @click="handleDelete(scope.$index, scope.row),dialogTableVisible3=false"
                > </el-button>
              </div>
            </el-dialog>
          </template>
        </el-table-column>
      </el-table>
    </template>
    <template>
      <div class="block">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage4"
          :page-sizes="[2, 3, 4, 5]"
          :page-size="everyPageNum"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalNum"
        >
        </el-pagination>
      </div>
    </template>
    <el-dialog
      title=""
      :visible.sync="dialogTableVisible"
    >
      <el-form
        ref="form"
        :model="form2"
        label-width="200px"
      >
        <el-form-item label=":">
          <el-input v-model="form2.bname"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form2.bsite"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form2.bdescribe"></el-input>
        </el-form-item>
      </el-form>
      <el-button
        type="primary"
        @click="onSubmit3"
      ></el-button>
    </el-dialog>
    <el-dialog
      title=""
      :visible.sync="dialogTableVisible2"
    >
      <el-form
        ref="form"
        :model="form3"
        label-width="200px"
      >
        <el-form-item label=":">
          <el-input v-model="form3.bname"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form3.bsite"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="form3.bdescribe"></el-input>
        </el-form-item>
      </el-form>
      <el-button
        type="primary"
        @click="onSubmit4"
      ></el-button>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    return {
      formInline: {
        bname: '',

      },
      tableData: '',
      currentPage4: '',
      totalNum: '',
      everyPageNum: 3,
      gridData: '',
      form2: {
        bname: '',
        bsite: '',
        bdescribe: '',
      },
      form3: {
        bname: '',
        bsite: '',
        bdescribe: '',
        bid: '',
      },
      dialogTableVisible: false,
      dialogTableVisible2: false,
      dialogTableVisible3: false,
    };
  },
  methods: {
    onSubmit1 () {
      var s = this.$qs.stringify(this.formInline);
      this.$axios.get('../product/selectbrand?' + s + "&pageSize=" + this.everyPageNum + "&currentPage=" + this.currentPage4)
        .then(response => {
          if (response.code == 20000) {
            this.tableData = response.data.list;
            this.totalNum = response.data.total;
            this.currentPage4 = response.data.pageNum;
            this.everyPageNum = response.data.pageSize;
          }
        }).catch(function (error) {
          console.log(error)

        })
    },
    handleSizeChange (val) {
      console.log(` ${val} `);
      this.everyPageNum = val;
      var s = this.$qs.stringify(this.formInline);
      this.$axios.get('../product/selectbrand?' + s + "&pageSize=" + this.everyPageNum + "&currentPage=" + this.currentPage4)
        .then(response => {
          if (response.code == 20000) {
            this.tableData = response.data.list;
            this.totalNum = response.data.total;
            this.currentPage4 = response.data.pageNum;
            this.everyPageNum = response.data.pageSize;
          }
        }).catch(function (error) {
          console.log(error)

        })
    },
    handleCurrentChange (val) {
      console.log(`: ${val}`);
      this.currentPage4 = val;
      var s = this.$qs.stringify(this.formInline);
      this.$axios.get('../product/selectbrand?' + s + "&pageSize=" + this.everyPageNum + "&currentPage=" + this.currentPage4)
        .then(response => {
          if (response.code == 20000) {
            this.tableData = response.data.list;
            this.totalNum = response.data.total;
            this.currentPage4 = response.data.pageNum;
            this.everyPageNum = response.data.pageSize;
          }
        }).catch(function (error) {
          console.log(error)

        })
    },
    handleEdit (index, row) {
      console.log(row);
      this.form3.bname = row.bname;
      this.form3.bsite = row.bsite;
      this.form3.bdescribe = row.bdescribe;
      this.form3.bid = row.bid;
    },
    handleDelete (index, row) {
      console.log(index, row);
      this.$axios.get('../product/deletebrand?bid=' + row.bid + "&pageSize=" + this.everyPageNum + "&currentPage=" + this.currentPage4)
        .then(response => {
          if (response.code == 20000) {
            this.totalNum = response.data.total;
            this.currentPage4 = response.data.pageNum;
            this.everyPageNum = response.data.pageSize;
          }
        }).catch(function (error) {
          console.log(error)

        }),
        this.onSubmit1();
    },
    onSubmit3 () {
      console.log("onsubmit3")
      var s = this.$qs.stringify(this.form2);
      this.$axios.get('../product/insertbrand?' + s + "&pageSize=" + this.everyPageNum + "&currentPage=" + this.currentPage4)
        .then(response => {
          if (response.code == 20000) {
            this.totalNum = response.data.total;
            this.currentPage4 = response.data.pageNum;
            this.everyPageNum = response.data.pageSize;
          }
        }).catch(function (error) {
          console.log(error)

        }),
        this.onSubmit1();

    },
    onSubmit4 () {
      var s = this.$qs.stringify(this.form3);
      console.log(s)
      this.$axios.get('../product/updatebrand?' + s + "&pageSize=" + this.everyPageNum + "&currentPage=" + this.currentPage4)
        .then(response => {
          if (response.code == 20000) {
            this.totalNum = response.data.total;
            this.currentPage4 = response.data.pageNum;
            this.everyPageNum = response.data.pageSize;
          }
        }).catch(function (error) {
          console.log(error)
        }),
        this.onSubmit1();
    }

  },
  components: {},
  computed: {},
  created () {
    this.$axios.get('../product/selectbrand')
      .then(response => {
        if (response.code == 20000) {
          this.tableData = response.data.list;
          this.totalNum = response.data.total;
          this.currentPage4 = response.data.pageNum;
          this.everyPageNum = response.data.pageSize;
        }
      }).catch(function (error) {
        console.log(error)

      })
  },
  mounted () { }
}
</script>

<style scoped>
</style>