<template>
  <div
    id="main"
  >
    <el-card style="margin-top: 20px;width: 86%">
      <el-col
        :span="3"
        style='margin-left: 30px;'
      >
        <el-input
          placeholder=""
          v-model="formValue.goodsname"
          clearable
          prefix-icon="el-icon-search"
        >
        </el-input>
      </el-col>
      <div
        class="block"
        style="display: inline-block;margin-left: 5px;"
      >
        <el-date-picker
          v-model="formValue.effectivetime"
          type="date"
          placeholder=""
          format="yyyy  MM  dd "
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </div>
      <div
        class="block"
        style="display: inline-block;margin-left: 5px"
      >
        <el-date-picker
          v-model="formValue.failuretime"
          type="date"
          placeholder=""
          format="yyyy  MM  dd "
          value-format="yyyy-MM-dd"
        >
        </el-date-picker>
      </div>
      <el-button
        type="primary"
        icon="el-icon-search"
        style='margin-left: 20px;'
        @click="mainQuery"
      >
        
      </el-button>
      <el-button
        type="success"
        icon="el-icon-sunrise"
        style='margin-left: 5px;'
        @click="mainQuery"
      >
        
      </el-button>
      <el-button
        type="success"
        icon="el-icon-sunny"
        style='margin-left: 5px;'
        @click="queryAudited"
      >
        
      </el-button>
      <el-button
        type="success"
        icon="el-icon-heavy-rain"
        style='margin-left: 5px;'
        @click="queryUnAudited"
      >
        
      </el-button>
    </el-card>

    <el-table
      ref="multipleTable"
      :data="tableData"
      tooltip-effect="dark"
      style="width: 86%"
      @selection-change="handleSelectionChange"
      highlight-current-row
      @current-change="handleCurrentChange"
      :header-cell-style="headClass"
      :cell-style="rowClass"
    >
      <el-table-column
        type="selection"
        width="55"
      >
      </el-table-column>
      <el-table-column
        prop="goodsname"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="goodstock"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="oldsale"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="newsale"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        prop="effectivetime"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="failuretime"
        label=""
        width="200"
      >
      </el-table-column>
      <el-table-column
        prop="auditstatus"
        label=""
        width="120"
      >
      </el-table-column>
      <el-table-column
        label=""
        width="520"
      >
        <template slot-scope="scope">
          <el-button
            type="warning"
            icon="el-icon-edit"
            style='margin-left: 5px;'
            @click="dialogFormVisible2 = true"
            v-if="scope.row.auditstatus == ''"
          >
            
          </el-button>
          <el-button
            type="success"
            icon="el-icon-full-screen"
            style='margin-left: 5px;'
            @click="viewDialogForm = true"
          >
            
          </el-button>
          <el-button
            type="primary"
            icon="el-icon-copy-document"
            style='margin-left: 5px;'
            @click="handleAuthorizationMenu(scope.$index, scope.row),trackVisible= true"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange1"
      :current-page.sync="page.pageNum"
      :page-sizes="[3, 4, 5, 6]"
      :page-size="page.pageSize"
      layout="sizes, prev, pager, next"
      :total="page.total"
    >
    </el-pagination>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="viewDialogForm"
    >
      <table
        border="1px"
        cellpadding="20px"
        cellspacing="0"
        id="myTable"
      >
        <tr>
          <td class="onLeft">id</td>
          <td class="onRight"><span v-text="updateForm.goodsid"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.goodsname"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.goodstock"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight">
            <span></span>
            <span v-text="updateForm.oldprice"></span>
            <span style="margin-left: 200px"></span>
            <span v-text="updateForm.oldsale"></span>
          </td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight">
            <span></span>
            <span v-text="updateForm.newprice"></span>
            <span style="margin-left: 200px"></span>
            <span v-text="updateForm.newsale"></span>
          </td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.effectivetime"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.failuretime"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.reasons"></span></td>
        </tr>
        <tr>
          <td class="onLeft"></td>
          <td class="onRight"><span v-text="updateForm.remark"></span></td>
        </tr>
      </table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="viewDialogForm = false"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="dialogFormVisible2"
    >
      <el-form
        :model="updateForm"
        ref="insertFormRe"
      >
        <table
          border="1px"
          cellpadding="20px"
          cellspacing="0"
          id="myTable"
        >
          <tr>
            <td class="onLeft">id</td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="updateForm.goodsid"
                  autocomplete="off"
                  disabled
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.goodsname"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.goodstock"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="updateForm.oldprice"
                  autocomplete="off"
                  placeholder=""
                  disabled
                ></el-input>
              </el-col>
              <el-col
                span="5"
                style="margin-left: 60px;"
              >
                <el-input
                  v-model="updateForm.oldsale"
                  placeholder=""
                  disabled
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-col span="5">
                <el-input
                  v-model="updateForm.newprice"
                  autocomplete="off"
                  placeholder=""
                  disabled
                ></el-input>
              </el-col>
              <el-col
                span="5"
                style="margin-left: 60px;"
              >
                <el-input
                  v-model="updateForm.newsale"
                  placeholder=""
                  disabled
                ></el-input>
              </el-col>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <div
                class="block"
                style="display: inline-block;margin-left: 5px;"
              >
                <el-date-picker
                  v-model="updateForm.effectivetime"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                  disabled
                >
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <div
                class="block"
                style="display: inline-block;margin-left: 5px;"
              >
                <el-date-picker
                  v-model="updateForm.failuretime"
                  type="date"
                  placeholder=""
                  format="yyyy  MM  dd "
                  value-format="yyyy-MM-dd"
                  disabled
                >
                </el-date-picker>
              </div>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.reasons"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.remark"
                autocomplete="off"
                disabled
              ></el-input>
            </td>
          </tr>
          <tr>
            <td class="onLeft"></td>
            <td class="onRight">
              <el-input
                v-model="updateForm.auditopinion"
                autocomplete="off"
              ></el-input>
            </td>
          </tr>
        </table>
      </el-form>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogFormVisible2 = false"> </el-button>
        <el-button
          type="warning"
          @click="dialogFormVisible2 = false, unAdopt()"
        >  </el-button>
        <el-button
          type="primary"
          @click="dialogFormVisible2 = false, adopt()"
        > </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=":"
      :visible.sync="trackVisible"
    >
      <el-table
        :data="track"
        tooltip-effect="dark"
        style="width: 70%"
        @selection-change="handleSelectionChange"
        highlight-current-row
        @current-change="handleCurrentChange"
        :header-cell-style="headClass"
        :cell-style="rowClass"
      >
        <el-table-column
          prop="approner"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="aduittime"
          label=""
          width="250"
        >
        </el-table-column>
        <el-table-column
          prop="auditstatus"
          label=""
          width="120"
        >
        </el-table-column>
        <el-table-column
          prop="auditopinion"
          label=""
          width="150"
        >
        </el-table-column>
      </el-table>
      <div
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="trackVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      calc: '',
      tableData: [],
      multipleSelection: [],
      page: {
        // pageSize: "",
        // pageNum: "",
        // total: "",
        // pages: ""
      },
      formValue: {},
      delNum: '',
      // 
      viewDialogForm: false,
      // 
      formLabelWidth: '120px',
      dialogFormVisible2: false,
      trackVisible: false,
      updateForm: {},
      track: []
    };
  },
  methods: {
    mainQuery () {
      this.formValue.auditstatus = 0;
      this.query();
    },
    queryAudited () {
      this.formValue.auditstatus = 1;
      this.query();
    },
    queryUnAudited () {
      this.formValue.auditstatus = 2;
      this.query();
    },
    insert () {
      this.$axios.post("/audit", this.insertForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        });
    },
    adopt () {
      this.updateForm.auditstatus = 1;
      this.update();
    },
    unAdopt () {
      this.updateForm.auditstatus = 2;
      this.update();
    },
    update () {
      this.$axios.post("/audit/createRecord", this.updateForm)
        .then(res => {
          if (res.code == 20000) {
            this.$message({
              message: '',
              type: 'success'
            });
          }
          this.query();
        });
    },
    query (param) {
      let data = param;
      if (this.formValue != undefined) {
        Object.assign(this.formValue, param);
        data = this.formValue;
        // console.log(data);
      }
      this.$axios.get("/audit/levelForm", {
        params: data
      })
        .then(res => {
          this.tableData = res.data.list; // 
          this.page = res.data; // 
        })
    },
    handleSizeChange (pageSize) { // 
      this.query({
        "pageNum": this.page.pageNum,
        "pageSize": pageSize
      })
    },
    handleCurrentChange1 (pageNum) { // 
      this.query({
        "pageNum": pageNum,
        "pageSize": this.page.pageSize
      });
    },
    handleSelectionChange (val) {
      if (val != undefined) {
        this.multipleSelection = val;
      }
    },
    handleCurrentChange (val) {
      if (val != undefined) {
        console.log(val);
        this.updateForm = val;
        this.delNum = val.id;
        this.trackId = val.goodsid;
      }
    },
    headClass () { //
      return "text-align:center"
    },
    rowClass () { //
      return "text-align:center"
    },
    calcprice () {
      this.insertForm.newsale = this.insertForm.oldsale * this.calc;
    },
    handleAuthorizationMenu (index, row) {
      this.getAllTrack(row.goodsid);
    },
    getAllTrack (param) {
      this.$axios.get("/audit/trace?id=" + param)
        .then(res => {
          this.track = res.data;
          console.log(this.track);
        });
    }
  },
  created () {
    this.mainQuery();
  },
}
</script>

<style scoped>

.onLeft {
  text-align: right;
}

.onRight {
  width: 500px;
}

#myTable {
  border: 1px lightblue solid;
}
</style>