<!--   todo -->
<template>
<div id='main'>
<el-card class="box-card">

  <!--  -->
  <el-form :inline="true" class="demo-form-inline">
    <el-form-item label=":">
      <el-input v-model="searchForm.deptName" @keyup.enter.native="search"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="success" @click="search"></el-button>
      <el-button type="warning" @click="reset"></el-button>
        <el-button type="info" @click="handleInsert"></el-button>
    </el-form-item>
  </el-form>

  <!-- table -->
  <el-table :data="tableData" style="width: 50%; margin-left:25%" >
    <el-table-column prop="deptId" label="" width="300%">
    </el-table-column>
    <el-table-column prop="deptName" label="">
    </el-table-column>

    <el-table-column label="" width="150px">
      <template slot-scope="scope">
        <!--  -->
        <el-button size="mini" @click="handleEdit(scope.row),operatedRowId=scope.$index"></el-button>
        <!-- bookIdbookIdDialog -->
        <el-button type="danger" size="mini"  @click="deletionId=scope.row.deptId, delDialog=true"></el-button>
      </template>
    </el-table-column>
  </el-table>

  <!--  -->
  <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
    :current-page.sync="page.pageNum" :page-sizes="[2, 3, 5, 10]" :page-size="page.pageSize"
    layout="sizes, prev, pager, next" :total="page.total">
  </el-pagination>

    <!--  -->
  <el-dialog title="" :visible.sync="delDialog" width="30%">
    <span>, ?</span>
    <span slot="footer" class="dialog-footer">
      <el-button @click="delDialog= false"> </el-button>
      <el-button type="primary" @click="delDialog = false,deleteById()"> </el-button>
    </span>
  </el-dialog>

    <!---->
  <el-dialog title="" :visible.sync="addDialog" width="30%">
    <el-form>
      <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="addForm.deptName" @input="change($event)"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
        <el-button @click="clearForm()"> </el-button>
        <el-button type="primary" @click="handleEvent(), clearForm()"> </el-button>
    </div>
  </el-dialog>
 
</el-card>
</div>
</template>

<script>
export default {
  data() {
    return {
      formLabelWidth: '25%',
      tableData: [],
      deptList:[],
      page: {}, //pageSize,pageNum,total
      // row index
      operatedRowId:'',
      // id
      deletionId:-1,
      delDialog:false,//Dialog
      addDialog:false,
      action:'',
      searchForm:{},
      addForm:{
        deptName : '',
      },
      roleList:{}
    }
  },
  methods: {
    change(){
      this.$forceUpdate()
    },
    clearForm(){
      this.addDialog = false
      this.addForm = {}
    },

    handleEvent(){
      if(this.action=='insert'){
        this.$axios.post("/dept/insertDept", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }else{
        this.$axios.put("/dept/updateDept", this.addForm).then(res => {
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
        })
      }
    },

    handleEdit(row){
      this.addDialog = true;
      this.addForm.deptId = row.deptId
      this.addForm.deptName = row.deptName;
      this.action = 'update';
    },

    handleInsert(){
      this.addForm = {}
      this.addDialog=true
      this.action='insert'
    },

    deleteById(){
      this.$axios.delete("/dept/deleteDept/"+this.deletionId).then(res=>{
          if(res.code == 20000){
            this.handlePageQuery(this.page.pageNum, this.page.pageSize)
            this.$message({
              message: '',
              type: 'success'
            });
          }else{
            this.$message({
              message: '',
              type: 'error'
            });
          }
      })
    },

    reset(){
      this.searchForm={}
      this.handlePageQuery(this.page.pageNum, this.page.pageSize)
    },
    search(){
      let param={pageNum:this.page.pageNum,pageSize:this.page.pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/dept/searchDept",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
      // this.$axios.post("/user/searchUsers", this.searchForm,{params:{pageNum:1,pageSize:3}}).then(res => {
      //   this.tableData = res.data.list;
      //   // this.page = res.data;
      // })
    },
    query() {
      this.$axios.get("/dept/getAllDeptWithPage").then(res => {
        this.tableData = res.data.list; //
        this.page = res.data;
      })
    },
    handleSizeChange(pageSize) {
      this.handlePageQuery(this.page.pageNum, pageSize)
    },
    handleCurrentChange(pageNum) {
      this.handlePageQuery(pageNum, this.page.pageSize)
    },
    //
    handlePageQuery(pageNum, pageSize){
      let param={pageNum:pageNum,pageSize:pageSize};
      Object.assign(param,this.searchForm)
      this.$axios.get("/dept/searchDept",{params:param}).then(res => {
        this.tableData = res.data.list;
        this.page = res.data;
      })
    }
  },
  created() {
    this.query();
  }
}
</script>

<style scoped>
#main{
  /* border: 1px solid black; */
  margin-left: 3%;
  margin-right: 3%;
}
.page {
  float: right;
  margin-top: 20px;
}
.el-pagination {
    white-space: nowrap;
    padding: 15px 5px;
    color: #303133;
    font-weight: 700;
}
.selectBoxWidth {
  /* width: 100%; */
  text-align: left;
  width:110px;
}
.addBoxWidth{
  width: 100%;
}
</style>